import React from 'react'
import { SelectorDown, SelectorUp } from './svg/selectorIcons'

import { applyScale, useScale } from '../contexts'
import { applyForAll } from '../iframeHelpers'
const scaleSteps = [1, 1.5, 2, 3]

export default function ScaleSelector({ selectorPosition }) {
  const { scale, setScale } = useScale()

  // Уменьшить шрифт
  const previousScaleStep = (e) => {
    e.stopPropagation()
    const currentScaleStepIndex = scaleSteps.indexOf(scale)
    const nextScaleStep = scaleSteps[currentScaleStepIndex - 1]
    if (!nextScaleStep) return
    setScale(nextScaleStep)

    if (nextScaleStep === scaleSteps[0]) {
      document.body.classList.remove('globalScaleMode')
      applyScale({ koeff: scaleSteps[0] })
      applyForAll()
    } else {
      applyScale({
        koeff: nextScaleStep,
        selector: `globalScaleMode__${nextScaleStep
          .toString()
          .replace('.', '_')}`
      })
      applyForAll()
    }
  }
  // Увеличить шрифт
  const nextScaleStep = (e) => {
    e.stopPropagation()
    const currentScaleStepIndex = scaleSteps.indexOf(scale)
    const nextScaleStep = scaleSteps[currentScaleStepIndex + 1]
    if (!nextScaleStep) return
    setScale(nextScaleStep)

    if (nextScaleStep === scaleSteps[1]) {
      document.body.classList.add('globalScaleMode')
    }
    applyScale({
      koeff: nextScaleStep,
      selector: `globalScaleMode__${nextScaleStep.toString().replace('.', '_')}`
    })
    applyForAll()
  }
  // Сбросить увеличение шрифтов
  const clearScale = (e) => {
    e.stopPropagation()
    if (!scale) return
    setScale(scaleSteps[0])
    document.body.classList.remove('globalScaleMode')
    applyScale({ koeff: scaleSteps[0] })
    applyForAll()
  }

  return (
    <div className='modifiersSelector' style={{ bottom: selectorPosition }}>
      <div
        data-voicecontrol='Уменьшить'
        onClick={previousScaleStep}
        style={scale === scaleSteps[0] ? { opacity: 0 } : {}}
      >
        <SelectorUp />
      </div>

      <div className='modifiersSelector_colors'>
        <div className='modifiersSelector_whiteSq_font'>{scale}x</div>
      </div>

      <div
        data-voicecontrol='Увеличить'
        onClick={nextScaleStep}
        style={
          scale === scaleSteps[scaleSteps.length - 1] ? { opacity: 0 } : {}
        }
      >
        <SelectorDown />
      </div>
      <div className='modifiersSelector_reset' onClick={clearScale}>
        Сбросить
      </div>
    </div>
  )
}
