import React, { memo } from 'react'

function LogoAndTitle({ title, logo, contentPath }) {
  const logoUrl = logo.name && contentPath ? contentPath + '\\' + logo.name : ''

  return (
    <section className='logoAndTitle'>
      {logoUrl && <img src={logoUrl} alt='logo' />}
      <h1 data-voicecontrol={title} className='secondaryColor'>
        {title}
      </h1>
    </section>
  )
}

export default memo(LogoAndTitle)
