import React, {useState, useContext, useRef, useEffect} from 'react'
import { Link } from 'react-router-dom'
import {ReviewContext} from '../contexts'
import arrowLeftIcon from '../assets/ArrowLeft.svg'
import arrowRightIcon from '../assets/ArrowRight.svg'
import Keyboard from './Keyboard';
import {
  HighButtonContext
} from '../contexts'

import './Review.css'

export default function ReviewText() {

  const {setReview, review} = useContext(ReviewContext)
  const [textInput, setTextInput] = useState(review.text)
  const textAreaRef = useRef()

  const isTooHight = useContext(HighButtonContext)

  useEffect(() => {
    const mainContainer = document.querySelector('.mainContentContainer')
    if (isTooHight) {
      mainContainer.style.top = '20%'
    } else {
      mainContainer.style.top = ''
    }
    return () => {
      mainContainer.style.top = ''
    }
  }, [isTooHight])

  const changeText = (text) => {
    setTextInput(text)
  }

  // Записал данные в глобальный state
  const changeData = () => {

    const text = textInput ? textInput : "Данные не заполнялись"
    setReview({ ...review, text, kind: 'text' })
  }

  const clearText = () => {
    setTextInput(textInput.slice(0, -1))
  }

  const scrollUp = () => {
    const scrollStep = textAreaRef.current.scrollHeight / 4
    textAreaRef.current.scrollTop -= scrollStep
  }

  const scrollDown = () => {
    const scrollStep = textAreaRef.current.scrollHeight / 4
    textAreaRef.current.scrollTop += scrollStep
  }

  return (
    <>
      <div className="keyboardScreenContainer">
        <div className="reviewWrapper">
          <h5 className="reviewTitle">1 шаг из 4</h5>
          <h4 className="reviewSubTitle">Текст отзыва</h4>

          <div className="reviewTextWrap">
            <textarea
              ref={textAreaRef}
              className="reviewText"
              value={textInput}
              onChange={e => setTextInput(e.target.value)}
            />
            <div className='reviewTextControls'>
              <button onClick={scrollUp} className='up'><img src={arrowLeftIcon} alt="up" /></button>
              <button onClick={scrollDown} className='down'><img src={arrowRightIcon} alt="up" /></button>
            </div>
          </div>
          {/* <span className="reviewLine"/> */}
          <Keyboard
            changeText={changeText}
            textInput={textInput}
            clearText={clearText}
          />
          <Link className="reviewButtonBig" to="/review/stars" onClick={changeData}>Указать оценку</Link>
        </div>
      </div>
    </>
  )
}

