import React, { useContext, useEffect, useState, useRef } from 'react'
import { Redirect, useParams, Link } from 'react-router-dom'
import classNames from 'classnames'

import { ConfigurationContext } from '../contexts'
import { applyForIframe } from '../iframeHelpers'

import ExternalWebview from './ExternalWebview'
import { ContentLeft, ContentRight } from './svg/contentArrows'
import { PlayButton, PauseButton, BackNextButton } from './svg/mediaIcons'
import { pathJoin } from '../func'

function InternalWebview({ fileName }) {
  const webviewRef = useRef(null)

  const style = {
    display: 'inline-flex',
    width: '640px',
    height: '480px'
  }
  return (
    <webview
      ref={webviewRef}
      src={`${fileName}`}
      className='contentFrame'
      style={style}
    />
  )
}

function ContentSection({ contentPath, section, offset }) {
  const [videoIsPlaying, setPlayVideo] = useState()
  const [audioIsPlaying, setPlayAudio] = useState(false)
  const audioTimeShift = 30

  const video = useRef(null)
  const audio = useRef(null)

  // useEffect(() => {
  //   console.log('video is playing', videoIsPlaying)
  // }, [videoIsPlaying])

  const playContent = (ref, setPlayRef) => {
    return (e) => {
      if (e && e.stopPropagation) {
        e.stopPropagation();
      }

      ref.current.play()
      setPlayRef(true)
    }
  }

  const pauseContent = (ref, setPlayRef, invisible) => {
    return (e) => {
      if (e && e.stopPropagation) {
        e.stopPropagation();
      }

      ref.current.pause()
      setPlayRef(false)
    }
  }

  let observer = new IntersectionObserver((entries) => {
    entries.forEach((entry) => {
      if (!entry.isIntersecting) {
        entry.target.pause()
        setPlayVideo(false)
      }
    });
  });

  useEffect(() => {
    if (video.current) {
      observer.observe(video.current)
    }
  }, [video.current])
      

  const shiftAudioRecord = (direction) => {
    const resetCurrentTime = () => {
      onPlayPauseAudio()
      audio.current.currentTime = 0
    }

    const currentTime = audio.current.currentTime
    const duration = audio.current.duration
    switch (direction) {
      case '+': {
        if (currentTime + audioTimeShift > duration) {
          resetCurrentTime()
        } else {
          audio.current.currentTime += audioTimeShift
        }
        break
      }
      case '-': {
        if (currentTime === 0) break
        if (currentTime - audioTimeShift < 0) {
          resetCurrentTime()
        } else {
          audio.current.currentTime -= audioTimeShift
        }
        break
      }
      default:
        return
    }
  }

  const onPlayPauseAudio = (e) => {
    if (e && e.stopPropagation) {
      e.stopPropagation();
    }

    if (audioIsPlaying) {
      pauseContent(audio, setPlayAudio)()
    } else {
      playContent(audio, setPlayAudio)()
    }
  }

  const onLoad = (e) => {
    applyForIframe(e.target)
  }

  if (section.type === 'text') {
    const fileName = pathJoin(contentPath, section.name)
    return <iframe className='contentFrame' src={fileName} onLoad={onLoad} />
  }
  if (section.type === 'exturl') {
    return <ExternalWebview section={section} />
  }
  if (section.type === 'image' || section.type === 'pdfppt') {
    const fileName = pathJoin(contentPath, section.name)

    return (
      <div className='contentImageContainer'>
        <img className='contentImage' src={fileName} />
      </div>
    )
  }
  if (section.type === 'video') {
    const fileName = pathJoin(contentPath, section.name)
    const cn = classNames('contentVideo_button', {
      content_playButton: !videoIsPlaying,
      content_hideButton: videoIsPlaying
    })

    return (
      <div className='contentImageContainer'>
        <div className='contentVideoWrap'>
          <video
            className='contentVideo'
            ref={video}
            onClick={pauseContent(video, setPlayVideo)}
            onEnded={() => setPlayVideo(false)}
          >
            <source
              src={fileName}
              type='video/mp4; codecs="avc1.42E01E, mp4a.40.2"'
            />
          </video>
          <button className={cn} onClick={playContent(video, setPlayVideo)}>
            <PlayButton />
          </button>
        </div>
      </div>
    )
  }
  if (section.type === 'audio') {
    const fileName = pathJoin(contentPath, section.name)
    const cn = classNames('contentAudio_main', {
      content_playButton: !audioIsPlaying,
      content_pauseButton: audioIsPlaying
    })
    setTimeout(() => {
      //if (!isVisible() && audio.current) pauseContent(audio, setPlayAudio)()
    }, 10)

    return (
      <div className='contentAudio'>
        <audio ref={audio} onEnded={() => setPlayAudio(false)}>
          <source src={fileName} type='audio/mpeg' />
        </audio>
        <span className='contentAudio_shift'>-{audioTimeShift}с.</span>
        <button
          className='contentAudio_control control_back'
          onClick={() => shiftAudioRecord('-')}
        >
          <BackNextButton />
        </button>
        <button className={cn} onClick={onPlayPauseAudio}>
          {!audioIsPlaying && <PlayButton size='132' />}
          {audioIsPlaying && <PauseButton size='132' />}
        </button>
        <button
          className='contentAudio_control control_next'
          onClick={() => shiftAudioRecord('+')}
        >
          <BackNextButton />
        </button>
        <span className='contentAudio_shift'>+{audioTimeShift}с.</span>
      </div>
    )
  }
  if (section.type === 'ziphtml') {
    const fileName = section.files[0] || '' 

    //seems like internal webview must support more than one .html file, but...
    const filePath = pathJoin(contentPath, section.name, fileName)

    return <InternalWebview fileName={filePath} />
  }

  return <></>
}

export function ContentRaw({ offset, setOffset, content, contentPath }) {
  let screenWidth = window.innerWidth

  useEffect(() => {
    screenWidth = window.innerWidth
  }, [window.innerWidth])
  const { sections = [], children = [] } = content
  const onLeftClick = () => {
    setOffset((offset) => (offset > 0 ? offset - 1 : 0))
  }
  const onRightClick = () => {
    setOffset((offset) => (offset < sections.length - 1 ? offset + 1 : offset))
  }

  const plainStyles = {
    left: `${offset * -screenWidth}px`
  }

  return (
    <div className='webviewWrapper'>

      <h2
        className='homeHeaderSubTitle secondaryColor'
        data-voicecontrol={content.title}
      >
        {content.title}
      </h2>

      {sections.length > 0 && (
        <div className='contentSectionsContainer'>
          <div className='contentSections' style={plainStyles}>
            {sections.map((section, index) => {
              if (section.type === 'image' || section.type === 'pdfppt') {
                return (
                  <div className='contentSections_sectionWrap' key={section.id}>
                    <ContentSection
                      section={section}
                      contentPath={contentPath}
                      offset={`${index * -screenWidth}px`}
                    />
                  </div>
                )
              }
              return (
                <div className={classNames('contentSections_sectionWrap', { ['site']: section.type === 'exturl' }, { ['audio']: section.type === 'audio' })} key={section.id}>
                  {/*<div className='contentSections_sectionWrap' key={section.id}>*/}
                  <ContentSection
                    section={section}
                    contentPath={contentPath}
                    offset={`${index * -100}vw`}
                  />
                </div>
              )
            })}
          </div>
          {offset > 0 && (
            <span className='contentLeftButton' onClick={onLeftClick}>
              <ContentLeft />
            </span>
          )}
          {offset < sections.length - 1 && (
            <span className='contentRightButton' onClick={onRightClick}>
              <ContentRight />
            </span>
          )}
        </div>
      )}

      {children.length > 0 && (
        <div className='cardWrapper'>
          {children
            .filter((i) => i.enabled)
            .map((child) => (
              <Link
                to={`/content/${child.name}`}
                key={child.name}
                className='card'
              >
                {child.title}
              </Link>
            ))}
        </div>
      )}
    </div>
  )
}

export default function Content() {
  const { contentId } = useParams()
  const configurationData = useContext(ConfigurationContext)

  const { contentPath } = configurationData

  const content = configurationData.content[contentId]

  const [offset, setOffset] = useState(0)
  useEffect(() => {
    setOffset(0)
  }, [content])

  if (!content) {
    return <Redirect to={{ pathname: '/' }} />
  }

  return (
    <ContentRaw
      offset={offset}
      setOffset={setOffset}
      content={content}
      contentPath={contentPath}
    />
  )
}
