import React, {
    useEffect,
    useState,
} from 'react'
import { spawn } from 'child_process'
import path from 'path'
import fs from 'fs'
import killTree from 'tree-kill'
  
const exePath = path.resolve('./temperature-sensor/UsbHid.exe')
const dataOutputPath = path.resolve('./temperature-sensor/output.json')

console.log(exePath)

let temperatureSensorExe;

function spawnTemperatureSensorExe(onSensorData) {
    temperatureSensorExe = spawn(path.resolve('./temperature-sensor/UsbHid.exe'))

    temperatureSensorExe.stdout.on('data', (data) => {
        // console.log('data', data.toString())
        onSensorData(data.toString()) 
    })

    temperatureSensorExe.stderr.on('data', (data) => {
        console.log('error', data)
    })
}

function killTemperatureSensorExe(cb) {
    if (!temperatureSensorExe) {
      return;
    }

    killTree(temperatureSensorExe.pid, () => {
        temperatureSensorExe = null
    })
}

export function useTemperatureSensorData() {
    const [temperatureInside, setTemperatureInside] = useState()

    const onSensorData = (data) => {
        setTemperatureInside(data)
    }

    const getTemperature = () => {
        spawnTemperatureSensorExe(onSensorData)
    }

    useEffect(() => {
        getTemperature()
        return () => {
            killTemperatureSensorExe()
        }
    }, [])

    return [temperatureInside, setTemperatureInside]
}