import React, { useEffect, useRef, useState } from "react";
import xor from "lodash/xor";
import { getMediaDeviceIds, getVideoStream, setVideoStream } from "../utils/manipulateVideoStreams";
import { setHost, setTerminalName } from "../api";
import Title from "./Title";

function CameraChoiceItem({ videoRef, name, onChangeInput, show }) {
  const [current, setCurrent] = useState(null);
  const inputRef = {
    top: useRef(null),
    right: useRef(null),
    left: useRef(null)
  };

  const onChange = (value) => {
    return () => {
      setCurrent(value);
      onChangeInput();
    }
  };

  useEffect(() => {
    if (!show && current) {
      setCurrent(null);
      Object.keys(inputRef).forEach(key => inputRef[key].current.checked = false);
    }
  }, [show]);

  return (
    <section className="cameraChoiceItem" >
      <div className="video_incoming" >
        { show
          ? <span className="cameraChoiceItem_chosen" >&#10004;</span>
          : <video className="incomingVideo" ref={ videoRef } id={name} muted />
        }
      </div>
      <div className="cameraChoiceItem_settings" >
        <label className={ current === "top" ?  "cameraChoiceItem_settingsLabel" : null } >
          <input type="radio"
                 name={name}
                 value="top"
                 onChange={onChange("top")}
                 ref={inputRef.top}
          />
          Верхняя камера
        </label>
        <label className={ current === "right" ? "cameraChoiceItem_settingsLabel" : null } >
          <input type="radio"
                 name={name}
                 value="right"
                 onChange={onChange("right")}
                 ref={inputRef.right}
          />
          Правая камера
        </label>
        <label className={ current === "left" ? "cameraChoiceItem_settingsLabel" : null } >
          <input type="radio"
                 name={name}
                 value="left"
                 onChange={onChange("left")}
                 ref={inputRef.left}
          />
          Левая камера
        </label>
      </div>
    </section>
  )
}

