let currentStream

export default function ({setRootConfig, setConfigData, config}) {
  const videoRef = useRef(null)
  const [cams, setCams] = useState({})
  const [deviceIds, setDeviceIds] = useState([])
  const [step, setStep] = useState(-1)

  // const [ error, setError ] = useState(false);
  // const [ chosen, setChosen ] = useState({ 0: false, 1: false, 2: false });

  useEffect(() => {
    getMediaDeviceIds()
    .then((ids) => {
      console.log('device ids', ids)
      setDeviceIds(ids)        
      setStep(0)
    })
  }, []);

  useEffect(() => {
    if (deviceIds.length === 0 || step < 0 || !videoRef.current) {
      return
    }

    const deviceId = deviceIds[step]
    const video = videoRef.current

    getVideoStream(deviceId)
    .then((stream) => {
      console.log('stream', deviceId, stream)
      currentStream = stream
      if (stream) {
        setVideoStream(video, stream)
      }      
    })
    
    // if (!chosen[0]) {
    //   setStream(deviceIds[0], videoRef0.current)
    // }

    // if (chosen[0]) {
    //   stopStream(deviceIds[0])
    //   .then(() => setStream(deviceIds[1], videoRef1.current))      
    // }

    // if (chosen[1]) {
    //   stopStream(deviceIds[1])
    //   .then(() => setStream(deviceIds[2], videoRef2.current))      
    // }

  }, [deviceIds, step, videoRef]);

  const onCamChoosed = (result) => {
    if (currentStream) {
      console.log('has stream')
      currentStream.getTracks().forEach((track) => {
        track.stop()
      })
      currentStream = null
      console.log('prev stream cleared')
    }
    
    const deviceId = deviceIds[step]
    const resultCams = {
      ...cams,
      [result]: deviceId,
    }
    
    setCams(resultCams)

    if (step < 2) {
      setStep(step + 1)
    } else {
      config.cams = resultCams
      console.log('result config', config)

      window.createConfigJson(config);
      setRootConfig(() => ({...config}) );
      const newConfigurationData = window.krustGetConfigs();
      newConfigurationData.contentPath = window.getConfigFolder();
      setConfigData({...newConfigurationData, title: config.name});
      setHost(config.serverHost);
      setTerminalName(config.name)
    }
  }

  const onContinueWithoutCamerasClick = () => {
    config.cams = {}
    console.log('result config', config)
    window.createConfigJson(config);
    setRootConfig(() => ({...config}) );
    const newConfigurationData = window.krustGetConfigs();
    newConfigurationData.contentPath = window.getConfigFolder();
    setConfigData({...newConfigurationData, title: config.name});
    setHost(config.serverHost);
    setTerminalName(config.name)
  }

  // const setStream = (deviceId, video) => {
  //   return getVideoStream(deviceId)
  //     .then(stream => {
  //       video && setVideoStream(video, stream)
  //     });
  // };

  // const stopStream = (deviceId) => {
  //   return getVideoStream(deviceId)
  //     .then(stream => {
  //       stream.getTracks().forEach((track) => {
  //         track.stop()
  //       })
  //     });
  // }

  // const saveData = (e) => {
  //   e.preventDefault();
  //   if (!formRef.current) return;

  //   const cams = [formRef.current.cam0.value, formRef.current.cam1.value, formRef.current.cam2.value];
  //   console.log('choices', cams, deviceIds);

  //   if (xor(cams, ['top', 'right', 'left']).length > 0) {
  //     setError(true);
  //     setChosen({ 0: false, 1: false, 2: false });
  //     formRef.current.cam0.value = '';
  //     formRef.current.cam1.value = '';
  //     formRef.current.cam2.value = '';
  //     return;
  //   }

  //   config.cams = {
  //     [cams[0]]: deviceIds[0],
  //     [cams[1]]: deviceIds[1],
  //     [cams[2]]: deviceIds[2],
  //   };
  //   console.log(config);
  //   window.createConfigJson(config);
  //   setRootConfig(() => ({...config}) );
  //   const newConfigurationData = window.krustGetConfigs();
  //   newConfigurationData.contentPath = window.getConfigFolder();
  //   setConfigData({...newConfigurationData, title: config.name});
  //   setHost(config.serverHost);
  //   setTerminalName(config.name);
  // };

  return (
    <section className="startConfig" >
      <Title className="startConfig_title" >Настройте камеры терминала</Title>
      {
        deviceIds.length > 0 && 
        <div className="b-camera-config">        
          <video className="incomingVideo" ref={ videoRef }  muted />
          <div className="b-camera-config_buttons">
            <button onClick={() => onCamChoosed('right')} >Камера справа</button>
            <button onClick={() => onCamChoosed('left')} >Камера слева</button>
            <button onClick={() => onCamChoosed('top')} >Камера сверху</button>
          </div>
        </div>
      }
      {
        deviceIds.length < 1 &&
        <div className="b-no-camera">
          <p>Камер не обнаружено</p>

          <button className='startConfig_submit' onClick={onContinueWithoutCamerasClick}>
            Продолжить
          </button>
        </div>
      }
    </section>
  )
}


