import React, { memo, useContext, useEffect, useState } from 'react'
import { TemperatureContext } from '../contexts';
import { WeatherInnerIcon, WeatherOuterIcon } from './svg/weatherIcons'
import { useTemperatureSensorData } from './TemperatureSensor';
import './WeatherWidget.css';

function WeatherWidget() {
  const [time, setTime] = useState('00:00');
  const [innerTemperature, setInnerTemperature] = useState('0°');
  const [update, setUpdate] = useState();
  const { temperatureOutside, temperatureInside } = useContext(TemperatureContext);

  const getTime = () => {
    const time = new Date();
    const hours = time.getHours() < 10 ? `0${time.getHours()}` : time.getHours();
    const minutes = time.getMinutes() < 10 ? `0${time.getMinutes()}` : time.getMinutes();
    setTime(`${hours}:${minutes}`);
  };

  const getInnerTemperature = () => {
    // console.log('temp inner', temperatureInside)
    if (temperatureInside) {
      try {
        const temp = Math.round(JSON.parse(temperatureInside).temperature);
        setInnerTemperature(temp > 0 ? `+${temp}°` : `${temp}°`);
      } catch(err) {
        console.log(err);
      }
    }
  };

  useEffect(() => {
    getTime();
    setInterval(getTime, 10000);
    setUpdate(1);
    setInterval(() => setUpdate(prevState => ++prevState), 5000);
  }, []);

  useEffect(() => {
    getInnerTemperature();
  }, [update]);

  return (
    <div className='weatherWidget'>
      <p className='time'>{time}</p>
      <div className='weatherBlock'>
        {temperatureInside ? (
          <div className='weather'>
            <WeatherInnerIcon />
            <p className='weatherValue'>{innerTemperature}</p>
          </div>
         ) : ''}

        {temperatureOutside ? (
          <div className='weather'>
            <WeatherOuterIcon />
            <p className='weatherValue'>{temperatureOutside}°</p>
          </div>
        ) : ''}
      </div>
    </div>
  )
}

export default memo(WeatherWidget)