import superagent from 'superagent'
import nanoid from "nanoid";
const { machineIdSync } = require('node-machine-id')

let HOST
let TERMINAL_NAME

export async function fetchUpdateInfo(terminalName, currentVersion = '') {
  const url = `${HOST}/api/terminal/${terminalName}/update`
  console.log('fetchUpdateInfo@url', url)

  const resp = await superagent.get(url)
    .query({ currentVersion })
  const result = resp.body
  console.log('fetchUpdateInfo@result', result)

  return result
}

export async function postCreateChat() {
  const url = `${HOST}/api/chat`
  console.log('postCreateChat@url', url)

  const resp = await superagent.post(url)
    .send({ terminalName: TERMINAL_NAME })
  const result = resp.body
  console.log('postCreateChat@result', result)

  return result
}

export async function postCreateMessage(message) {
  const url = `${HOST}/api/chat/message`
  const toSend = {
    ...message,
    terminalName: TERMINAL_NAME,
  }

  const resp = await superagent.post(url)
    .send(toSend)
  const result = resp.body

  console.log('postCreateMessage@result', result)
  return result
}

export async function postCreateReview(obj) {
  console.log('postCreateReview', obj)

  let url = `${HOST}/api/review`
  console.log('postCreateChat@url', url)
  const resp = await superagent.post(url)
    .send({
      ...obj,
      terminalId: TERMINAL_NAME,
    })

  const result = resp.body

  if (!result && !result.id) {
    return
  }

  if (obj.kind === 'video' && obj.blobList) {
    url = `${HOST}/api/review/load-video/${result.id}`


    for (let i = 0; i < obj.blobList.length; i++) {
      const blob = obj.blobList[i]

      const form = new FormData()
      form.append('video-review', blob, 'video-review.webm')
      form.append('chunksCount', obj.chunksCount)
      form.append('currentChunk', i + 1)

      await superagent.post(url)
        .send(form)
    }
  }

  // TODO можно и по красивее сделать
  return result
}

export async function postVideoChat(blob, operator, durationInSeconds, terminalName) {
  const url = `${HOST}/api/terminal/video-record/${operator}/${terminalName}/${durationInSeconds}`;
  const form = new FormData();
  form.append('video-record', blob, 'video-record.webm');

  const resp = await superagent.post(url)
    .send(form);

  console.log('postVideoChat@result', resp.body)
  return resp.body;
}

export function setHost(host) {
  HOST = host
  console.log('Set api host', host)
}

export function setTerminalName(terminalName) {
  TERMINAL_NAME = terminalName
}

export async function postVideoInit(payload) {
  await superagent
    .post(`${HOST}/api/terminal/video-init`)
    .send({ ...payload, terminalName: TERMINAL_NAME })
}

export async function connectServer(rawHost, terminalName) {
  // host = 'localhost:8010'; // локальное тестирование
  const host = rawHost.replace(/^https?:\/\//, '')

  async function tryConnectServer(protocol) {
    const url = `${protocol}://${host}/api/server/init`;
    const res = await superagent.post(url)
      .send({ terminalName });
    const body = res.body;
    setHost(`${protocol}://${host}`);
    setTerminalName(terminalName)
    return { ...body, serverHost: `${protocol}://${host}`, name: terminalName };
  }

  try {
    return await tryConnectServer('https');
  } catch (e) {
    try {
      return await tryConnectServer('http');
    } catch (err) {
      throw new Error('Request has been terminated')
    }
  }
}

async function getSystemToken() {
  const machineId = machineIdSync()
  const appName = '4b696f736b' //'Kiosk' in ascii

  return `${appName}-${machineId}`
}

//TODO: CHANGE BEFORE DEPLOY!
const licenseActivationUrl = 'https://krust.tech/api/activate-license' //'https://krust.tech/api/activate-license'
const licenseCheckUrl = 'https://krust.tech/api/check-license'
const pingServerUrl = 'https://krust.tech/api/terminals/ping'

export async function activateLicense(licenseCode) {
  const systemToken = await getSystemToken();

  const requestUrl = `${licenseActivationUrl}/${licenseCode}?systemId=${systemToken}`

  try {
    const response = await superagent.get(requestUrl)

    if (!response.body)
      return { errorMessage: 'no response' }

    return response.body;
  }
  catch (e) {
    const errorMessage = e.response?.text || e.message

    return { errorMessage: errorMessage };
  }
}

export async function checkLicense() {
  const systemToken = await getSystemToken()

  const requestUrl = `${licenseCheckUrl}/?systemId=${systemToken}`

  try {
    const response = await superagent.get(requestUrl)

    if (!response.body)
      return { errorMessage: 'no response' }

    return response.body;
  }
  catch (e) {
    const errorMessage = e.response?.text || e.message

    return { errorMessage: errorMessage };
  }
}

export async function getWeather({ lat, lon }) {
  try {
    console.log('host', HOST)

    const response = await superagent.get(`${HOST}/api/weather?lat=${lat}&lon=${lon}`)

    return response.body
  }
  catch (e) {
    console.log('get weather error, ', e)
  }
}

//notify krust.tech about own existence
export async function pingServer(softwareVersion, licenseCodeStrId) {
  const systemToken = await getSystemToken()

  console.log('ping server, licenseCode', licenseCodeStrId)

  try {
    const response = await superagent.post(pingServerUrl).send({
      licenseCodeStrId,
      version: softwareVersion,
      systemId: systemToken
    })

    console.log('resp', response.body)

    return response.body
  }
  catch (e) {
    console.log('ping server error:')
    console.log(e)

    return e
  }
}