import React, { useContext, useEffect, useState } from 'react'
import { Link, Redirect } from 'react-router-dom'

import { ModifiersContext } from '../contexts'
import Title, { TitleH3 } from './Title'
import VideoCall from './VideoCall'

import './Sos.css'

const SosComponent = () => {
  const { modifiers } = useContext(ModifiersContext)
  const { colors } = modifiers
  const defaultTimerValue = 5
  const [time, setTime] = useState(defaultTimerValue)

  useEffect(() => {
    let intervalId = null

    if (time <= 0) {
      clearInterval(intervalId)
    } else {
      intervalId = setInterval(() => {
        setTime(time - 1)
      }, 1000)
    }

    return () => {
      clearInterval(intervalId)
    }
  }, [time])

  if (!time) {
    return <Redirect to={{ pathname: '/' }} />
  }

  return (
    <div>
      <section className='sosPage'>
        <TitleH3
          color={colors.primaryColor}
          className='sosTimer secondaryColor'
        >
          00:0{time}
        </TitleH3>
        <Title color={colors.primaryColor} className='sosTitle secondaryColor'>
          Вы уверены в необходимости вызова срочной помощи?
        </Title>
        <div className='sosButtonWrapper'>
          <Link className='sosButton' to='/'>
            Я ошибся
          </Link>
          <Link className='sosButton sosButton-yellow' to='/sos/call'>
            Да, уверен
          </Link>
        </div>
      </section>
    </div>
  )
}

export const SosCall = () => {
  return <VideoCall sosCall />
}

export default SosComponent
