import React, { useContext, useEffect } from 'react'
import { Link } from 'react-router-dom'
import { ConfigurationContext, useScreenMode } from '../contexts'

import './ScreenSaver.css'
import { pathJoin } from '../func'
import { homeIconList } from './svg/homeIcons'
import { GlobalControls, ModifiersContext } from '../contexts'
import WeatherWidget from './WeatherWidget'

export default function ScreenSaver() {
  const { backgroundVideo = {} } = useContext(ConfigurationContext)
  const configurationData = useContext(ConfigurationContext)
  const { contentPath } = configurationData
  const { voiceInductionToggle } = useContext(GlobalControls)
  const { Touch, Wide, Centered, Voice } = homeIconList
  const { modifiers } = useContext(ModifiersContext)
  const {
    colors: { isContrastMode }
  } = modifiers

  const uri = pathJoin(contentPath, backgroundVideo.name)

  // меняем видео на полный экран или горизонтально в окне по середине
  const styleBg = backgroundVideo.full
    ? 'screenSaverBgVideoFull'
    : 'screenSaverBgVideo'

  const { setScreenMode } = useScreenMode()

  useEffect(() => {
    let mountEl

    if (styleBg === 'screenSaverBgVideoFull') {
      mountEl = document.getElementById('outer-video-mount')
      mountEl.innerHTML = `
        <video class="${styleBg}" width="100%" height="100%" loop autoPlay>
          <source src="${uri}" type='video/mp4; codecs="avc1.42E01E, mp4a.40.2"' />
        </video>
      `
    }

    return () => {
      if (mountEl) {
        mountEl.innerHTML = ''
      }
    }
  }, [backgroundVideo])

  return (
    <>
      <Link className='screenSaverLink' to='/'>
        <div className='screenSaverWrap'>
          <div className='screenSaverWeather'>
            <WeatherWidget />
          </div>
          <div className='screenSaverIcon'>
            <Touch />
          </div>
          <div className='screenSaverTitle secondaryColor'>
            Коснитесь экрана, <br />
            чтобы начать работу с терминалом, <br />
            или выберите другой режим:
          </div>
          <div className='screenSaverButtons'>
            <button onClick={() => setScreenMode('wide')}>
              <Wide
                className={
                  isContrastMode ? 'primaryColor_fill' : 'secondaryColor_fill'
                }
              />
            </button>
            <button onClick={() => setScreenMode('centered')}>
              <Centered
                className={
                  isContrastMode ? 'primaryColor_fill' : 'secondaryColor_fill'
                }
              />
            </button>
            <button
              onClick={() => {
                voiceInductionToggle()
                window.krustVCIsEnabled = !window.krustVCIsEnabled
              }}
            >
              <Voice
                className={
                  isContrastMode ? 'primaryColor_fill' : 'secondaryColor_fill'
                }
              />
            </button>
          </div>
        </div>
        {!backgroundVideo.full && (
          <video className={styleBg} width='100%' height='100%' loop autoPlay>
            <source
              src={uri}
              type='video/mp4; codecs="avc1.42E01E, mp4a.40.2"'
            />
          </video>
        )}
      </Link>
    </>
  )
}
