import React from 'react'
import cn from 'classnames'

import './Title.css'

const Title = ({ children, className, ...rest }) => (
    <h1 className={cn('titleH1', { [className]: className })} {...rest}>{children}</h1>
)

export const TitleH3 = ({ children, className, ...rest }) => (
    <h3 className={cn('titleH3', { [className]: className })} {...rest}>{children}</h3>
)

export default Title
