import React, {useEffect, useState} from 'react'
import { Link } from 'react-router-dom'

import './Review.css'

export default function ChatChooseType() {
  const [webcam, setWebcam] = useState(true)


  useEffect(() => {
    let md = navigator.mediaDevices;
    if (!md || !md.enumerateDevices) return false;
    md.enumerateDevices().then(devices => {
      console.log('devices', devices)
      setWebcam(devices.some(device => 'videoinput' === device.kind))
    })
  }, [])

  return (
    <>
      <div className="SelectReviewWrapper">
        <h3 className="actionHeader" data-voicecontrol="Выберите способ связи с оператором" >
          Выберите способ<br/>связи с оператором
        </h3>
        <div className="ReviewButtonWrapper">
          { webcam &&
              <Link className="ReviewButton" to="/operator/videoCall" data-voicecontrol="Видео" >Видео</Link>
          }
          <Link className="ReviewButton" to="/chat" data-voicecontrol="Текстовый чат" >Текстовый чат</Link>
        </div>
      </div>
    </>
  )
}
