export function join(dir, source) {
  return `${dir}/${source}`.replace(/\\/g, '/')
}

export function url(...args) {  
  const [f, s] = args
  if (!f) {
    return `url('${s}')`
  }
  
  const joinedArgs = args.join('/') 
  return `url('${ joinedArgs.replace(/\\/g, '/') }')`
}

export function pathJoin(...args) {
  const [f, s] = args
  /*
    Предпросмотр использует data: адреса, чтобы без сервака делать предпросмотр HTML
    if должен учесть именно их
    А, f === undefined потому что contentPath пустой у предпросмотра :)
    А для простых урлов я тупо в нейм кладу норм путь. 
  */
  if (!f) { //  && s.indexOf('data:') === 0
    return s
  }
  return args.join('\\')
}
