import React from 'react'

import "./mediaIcons.css"

export function PlayButton({ size = 64 }) {
  return (
    <svg 
      width={size} 
      height={size} 
      viewBox="0 0 132 132" 
      fill="none" 
      xmlns="http://www.w3.org/2000/svg"
    >
      <rect 
        x="3" 
        y="3" 
        width="126" 
        height="126" 
        rx="39"           
        stroke-width="6"
        className="media-playRect"
      />
      <path 
        d="M86.6345 64.3573L56.1408 43.1811C54.8146 42.2601 53 43.2092 53 44.8238L53 87.1761C53 88.7908 54.8146 89.7399 56.1408 88.8189L86.6345 67.6427C87.7801 66.8472 87.7801 65.1528 86.6345 64.3573Z" 
        className="media-playPath"
      />
    </svg>

  )
}

export function PauseButton({ size = 64 }) {
  return (
    <svg 
      width={size} 
      height={size} 
      viewBox="0 0 132 132" 
      fill="none" 
      xmlns="http://www.w3.org/2000/svg"
    >
      <rect x="3" y="3" width="126" height="126" rx="39" 
        className="media-playRect"         
        stroke-width="6"
      />
      <path fill-rule="evenodd" clip-rule="evenodd" d="M49 43C47.8954 43 47 43.8954 47 45V87C47 88.1046 47.8954 89 49 89H59C60.1046 89 61 88.1046 61 87V45C61 43.8954 60.1046 43 59 43H49ZM73 43C71.8954 43 71 43.8954 71 45V87C71 88.1046 71.8954 89 73 89H83C84.1046 89 85 88.1046 85 87V45C85 43.8954 84.1046 43 83 43H73Z" 
        className="media-playPath"
      />
    </svg>
  )
}

export function BackNextButton() {
  return (
    <svg width="74" height="74" viewBox="0 0 74 74" fill="none" xmlns="http://www.w3.org/2000/svg">
      <circle 
        r="36.9054" 
        transform="matrix(1 8.74228e-08 8.74228e-08 -1 36.9054 37.0946)" 
        className="media-forwardCircle"
      />
      <path 
        d="M15.9889 34.4302L29.7612 23.5573C31.0728 22.5219 33.0005 23.4561 33.0005 25.1271L33.0005 46.8729C33.0005 48.5439 31.0728 49.4781 29.7612 48.4427L15.9889 37.5698C14.9746 36.769 14.9746 35.231 15.9889 34.4302Z" 
        className="media-forwardPath"
      />
      <path 
        d="M36.8442 34.4636L49.7201 23.7336C51.0228 22.6481 53.0005 23.5744 53.0005 25.2701L53.0005 46.7299C53.0005 48.4256 51.0228 49.3519 49.7201 48.2664L36.8442 37.5364C35.8847 36.7369 35.8847 35.2631 36.8442 34.4636Z" 
        className="media-forwardPath"
      />
    </svg>
  )
}