import React, { memo } from 'react'
import { useHistory, useLocation } from 'react-router-dom'
import { homeIconList } from '../svg/homeIcons'
import WeatherWidget from '../WeatherWidget'

function BackAndMain() {
  const history = useHistory()
  const location = useLocation()
  const { Home, Back } = homeIconList

  return (
    <div className='backAndMain'>
      <button data-voicecontrol='Назад'
        onClick={() => {
          if (location.pathname !== '/') {
            history.goBack()
          }
        }}
      >
        <Back />
      </button>
      {
        location.pathname !== '/' && (
          <button data-voicecontrol='Главный экран'
            onClick={() => history.push('/')}>
            <Home />
          </button>
        )
      }

      { location.pathname === '/' && <WeatherWidget /> }
      
    </div>
  )
}

export default memo(BackAndMain)
