import { getColors, getScale } from './contexts'
import { textToSpeech } from './sound'

function isOurIframe(iframe) {
  return true
}

export function applyForIframe(iframe) {
  const colors = getColors()
  const scale = getScale()
  registerClickHandler(iframe)
  return applyStyles(iframe, colors, scale)
}

const mountPoint = document.querySelector('#krust-mount-point')

export function applyForAll() {
  const iframes = document.querySelectorAll('iframe')
  const colors = getColors()
  const scale = getScale()
  for (const iframe of iframes) {
    applyStyles(iframe, colors, scale)
  }
  let scaleKoeff = 1
  if (scale) {
    scaleKoeff = scale.koeff
    mountPoint.className = `${scale.selector}`
  }

  // apply for external sites
  const { isContrastMode, primaryColor, secondaryColor } = colors
  const webviews = document.querySelectorAll('webview')
  let code = isContrastMode
    ? getCodeEnableContrast(primaryColor, secondaryColor, scaleKoeff)
    : getCodeDisableContrast(scaleKoeff)

  changeScale(scaleKoeff)

  for (const webView of webviews) {
    webView.executeJavaScript(code)
  }
}

export function applyStyles(iframe, colors, scale) {
  if (isOurIframe(iframe)) {
    return applyStylesOurContent(iframe, colors, scale)
  }
  return applyStylesExternalContent(iframe, colors, scale)
}

export function applyStylesOurContent(iframe, colors, scale) {
  let fontSize
  switch (scale.koeff) {
    case 1.5:
      fontSize = '36px'
      break
    case 2:
      fontSize = '42px'
      break
    case 3:
      fontSize = '48px'
      break
    default:
      fontSize = '30px'
  }
  const styleColors = `
    :root {
      --primary-color: ${colors.primaryColor};
      --secondary-color: ${colors.secondaryColor};
    }
    body {
      max-width: 100%;
    }
    body * {
      font-size: ${fontSize};
      line-height: ${fontSize};
    }
  `
  const styleNodeId = 'krust_style_colors'

  const doc = iframe.contentDocument

  if (!doc) {
    return
  }

  const sColors = doc.createElement('style')
  sColors.id = styleNodeId
  sColors.innerHTML = styleColors

  const prevColors = doc.getElementById(styleNodeId)
  if (prevColors) {
    prevColors.parentNode.removeChild(prevColors)
  }

  doc.head.appendChild(sColors)

  if (colors.isContrastMode) {
    if (!doc.body.classList.contains('globalContrastMode')) {
      doc.body.classList.add('globalContrastMode')
    }
  } else {
    doc.body.classList.remove('globalContrastMode')
  }

  for (const cl of doc.body.classList) {
    if (cl.indexOf('globalScaleMode') === 0) {
      doc.body.classList.remove(cl)
    }
  }
}

export function applyStylesExternalContent(iframe, colors, scale) {
  /*
    форс на цвета
    Получаем базовый размер шрифта
  */
}

function registerClickHandler(iframe) {
  const doc = iframe.contentDocument
  doc.addEventListener('click', textToSpeech)
}

function getCodeEnableContrast(primaryColor, secondaryColor) {
  return `
    ;(function(){
      const sColors = document.createElement('style');
      sColors.id = 'krust_style_colors';
      sColors.innerHTML = '* {color: ${primaryColor} !important; background: ${secondaryColor} !important;}
      document.head.appendChild(sColors)
    })();
  `
}

function changeScale(scale) {
  const webview = document.getElementById('wv')
  if (webview) {
    webview.setZoomFactor(scale)
  }
}

function getCodeDisableContrast() {
  return `
    ;(function(){
      const els = document.querySelectorAll('#krust_style_colors')
      for (const el of els) {
        el.parentNode.removeChild(el)
      }
    })();
  `
}
