import React, { useContext } from 'react'
import { useHistory } from 'react-router-dom'
import { GlobalControls } from '../contexts'

export default function TooHighButton({ top, setHighButton }) {
  const { setTooHigh } = useContext(GlobalControls)

  // Переместить экран вниз - вверх
  const onTooHighClick = () => {
    setTooHigh(true)
    document.body.classList.remove('globalTooHigh')
    setHighButton(false)
  }
  const onTooHighRemove = () => {
    setTooHigh(false)
    document.body.classList.add('globalTooHigh')
    setHighButton(true)
  }

  // Проверка по пути - отображается ли экран клавиатуры
  const history = useHistory()
  const location = history.location.pathname

  // Переключение стилей на верхнюю / нижнюю кнопку
  const style = top ? 'tooHighButton' : 'tooLowButton'
  const func = top ? onTooHighClick : onTooHighRemove
  const text = top ? 'Слишком низко' : 'Я не дотягиваюсь'
  // Если мы на экране клавиатуры - добавляется класс, меняющий цвет кнопки
  const keyboard =
    (/review\/\w+/.test(location) && !location.includes('review/finish')) ||
    location.includes('chat')
  const styleBlack = top ? 'tooHighButton' : 'tooLowButton'
  const isChat = location.includes('chat')
  const isReview = location.includes('review')
  const isLandscape = window.innerWidth > window.innerHeight

  if (isChat || (isReview && !isLandscape)) return null

  return (
    <>
      {keyboard ? (
        <button className={styleBlack} onClick={func} data-voicecontrol={text}>
          {text}
        </button>
      ) : (
        <button className={style} onClick={func} data-voicecontrol={text}>
          {text}
        </button>
      )}
    </>
  )
}
