import React, { useState, useContext, useEffect } from 'react'
import { Route, Switch, HashRouter } from 'react-router-dom'

import Home from './Home'
import VideoCall from './VideoCall'
import Content from './Content'
import Chat from './Chat'

import Review from './Review'
import ReviewText from './ReviewText'
import ReviewStars from './ReviewStars'
import ReviewName from './ReviewName'
import ReviewPhone from './ReviewPhone'
import ReviewFinish from './ReviewFinish'

import VideoRecord from './VideoRecord'
import ScreenSaver from './ScreenSaver'
import ChatChooseType from './ChatChooseType'
import SosComponent, { SosCall } from './Sos'
import Background from './Background'

import TooHighButton from './TooHighButton'
import { initButtonsSound } from '../sound'

import Header from './Header/Header'

import { HighButtonContext } from '../contexts'
import { TinyEmitter } from 'tiny-emitter'
import {
  ExternalWebviewKeyboardContext,
  ScreenModeContext,
  defaultScreenMode,
  ScaleContext,
  currentScale
} from '../contexts'
import { useTemperatureSensorData } from './TemperatureSensor'

initButtonsSound()

export default function Root() {
  const [isHighButton, setHighButton] = useState(false)
  const [screenMode, setScreenMode] = useState(defaultScreenMode)
  const [scale, setScale] = useState(currentScale.koeff)
  const [showExternalWebviewKeyboard, setShowExternalWebviewKeyboard] =
    useState(false)

  const isLandscape = window.innerWidth > window.innerHeight

  const is4K = window.innerWidth >= 3500

  if (is4K) {
    document.body.classList.add('wideScreen')
  }

  return (
    <HighButtonContext.Provider value={isHighButton}>
      <ExternalWebviewKeyboardContext.Provider
        value={{ showExternalWebviewKeyboard, setShowExternalWebviewKeyboard }}
      >
        <ScreenModeContext.Provider value={{ screenMode, setScreenMode }}>
          <ScaleContext.Provider value={{ scale, setScale }}>
            <HashRouter>
              <div
                className={`screenContainer ${isLandscape ? 'landscape' : ''}`}
                id='screenContainer'
              >
                <Background />

                {/* top - кнопка сверку, если нет - то кнопка снизу */}
                <TooHighButton top setHighButton={setHighButton} />

                <section className='mainContentContainer'>
                  <Header />
                  <Switch>
                    <Route path='/content/:contentId'>
                      <Content />
                    </Route>
                    <Route path='/videoCall'>
                      <ChatChooseType />
                    </Route>
                    <Route path='/operator/videoCall'>
                      <VideoCall />
                    </Route>
                    <Route path='/chat'>
                      <Chat />
                    </Route>
                    <Route path='/review/finish'>
                      <ReviewFinish />
                    </Route>
                    <Route path='/review/phone'>
                      <ReviewPhone />
                    </Route>
                    <Route path='/review/name'>
                      <ReviewName />
                    </Route>
                    <Route path='/review/stars'>
                      <ReviewStars />
                    </Route>
                    <Route path='/review/text'>
                      <ReviewText />
                    </Route>
                    <Route path='/review/video'>
                      <VideoRecord />
                    </Route>
                    <Route path='/review'>
                      <Review />
                    </Route>
                    <Route path='/screenSaver'>
                      <ScreenSaver />
                    </Route>
                    <Route path='/sos/call'>
                      <SosCall />
                    </Route>
                    <Route path='/sos'>
                      <SosComponent />
                    </Route>
                    <Route path='/'>
                      <Home slsk='1' />
                    </Route>
                  </Switch>
                </section>

                <TooHighButton top={false} setHighButton={setHighButton} />
              </div>
            </HashRouter>
          </ScaleContext.Provider>
        </ScreenModeContext.Provider>
      </ExternalWebviewKeyboardContext.Provider>
    </HighButtonContext.Provider>
  )
}

export const extWvEventPrefix = 'external-webview-keyboard-event'
export const extWvEmitter = new TinyEmitter()

