import React, { useState, useEffect, useContext } from 'react'
import { HighButtonContext } from '../contexts'

import { Redirect } from 'react-router-dom'

import { postCreateChat } from '../api'

import './OperatorChat.css'
import './Spinner.css'

import OperatorChat from './OperatorChat'
import OperatorChatFinish from './OperatorChatFinish'

function WaitConnection({ error = null, onRetry }) {
  return (
    <div className='operatorChatConnectionWrap'>
      {error ? (
        <div className='operatorChatConnectionIcon' />
      ) : (
        <div className='operatorChatConnectionLoading'>
          <div className='Spinner' />
        </div>
      )}
      <h2 className='operatorChatConnectionTitle secondaryColor'>
        {error
          ? 'Оператор сейчас не может ответить на ваш вызов.'
          : 'Соединяем со свободным оператором'}
      </h2>
      {error ? (
        <div className='operatorChatConnectionBtn' onClick={onRetry}>
          {' '}
          Повторить попытку
        </div>
      ) : (
        <h3 className='operatorChatConnectionDescription'>Текстовый чат</h3>
      )}
    </div>
  )
}

const DEFAULT_TIMER = 10 // in sec, yes

export default function Chat() {
  const [chat, setChat] = useState(true)
  const [textInput, setTextInput] = useState()
  const [messages, setMessages] = useState([])

  // ?
  const [error, setError] = useState(null)
  const [isFinishShown, setFinishShowFlag] = useState(false)
  const [isRedirectToMain, setRedirectToMainFlag] = useState(false)

  const [timerCount, setTimerCount] = useState(DEFAULT_TIMER)

  const isTooHight = useContext(HighButtonContext)

  const onConnectRetry = () => {}

  useEffect(() => {
    // Создаём новый чат на каждый mount
    postCreateChat()
      .then(setChat)
      .catch((err) => {
        console.log('chat:create@err', err)
      })
  }, [])

  useEffect(() => {
    if (!isFinishShown) {
      setTimerCount(DEFAULT_TIMER)
      return
    }
    const ti = setInterval(() => {
      setTimerCount((timerCount) => timerCount - 1)
    }, 1000)
    const to = setTimeout(() => {
      setRedirectToMainFlag(true)
    }, DEFAULT_TIMER * 1000)
    return () => {
      clearInterval(ti)
      clearTimeout(to)
    }
  }, [isFinishShown])

  useEffect(() => {
    if (chat && !isFinishShown) {
      document.body.classList.add('global_keyboard_shown')
    } else {
      document.body.classList.remove('global_keyboard_shown')
    }
  }, [chat, isFinishShown])

  const onCancelClick = () => {
    setFinishShowFlag(false)
  }

  const onConfirmClick = () => {
    setRedirectToMainFlag(true)
  }

  if (isRedirectToMain) {
    return <Redirect to={{ pathname: '/' }} />
  }

  if (isFinishShown) {
    return (
      <>
        <OperatorChatFinish
          timerCount={timerCount}
          onCancelClick={onCancelClick}
          onConfirmClick={onConfirmClick}
        />
      </>
    )
  }

  const isLandscape = window.innerWidth > window.innerHeight

  return (
    <div
      className='keyboardScreenContainer chat'
      style={
        isTooHight && !isLandscape ? { transform: 'translateY(-25%)' } : {}
      }
    >
      {!chat && <WaitConnection error={error} onRetry={onConnectRetry} />}
      {chat && (
        <OperatorChat
          chat={chat}
          textInput={textInput}
          setTextInput={setTextInput}
          messages={messages}
          setMessages={setMessages}
        />
      )}
    </div>
  )
}
