import React, { useState, useContext, useEffect } from 'react'
import { Link, Redirect } from 'react-router-dom'
import classNames from 'classnames'
import lodash from 'lodash'
import './Home.css'
import './ScreenSaver.css'
import { homeIconList } from './svg/homeIcons'
import {
  ConfigurationContext,
  useScreenMode,
  useScale,
  HighButtonContext
} from '../contexts'
import Slider from './Slider/Slider'
import { ipcRenderer } from 'electron'

function MenuElement({ name, kind, title }) {
  const Icon = homeIconList[name] || homeIconList['globe']
  const iconCl = classNames({
    homeIcon: true,
    [name]: true
  })
  const icon = (
    <span className={iconCl} data-voicecontrol={title}>
      <Icon />
    </span>
  )

  if (kind === 'screen') {
    return (
      <Link className='homeCard' to={`/${name}`} data-voicecontrol={title}>
        {icon}
        <span>{title}</span>
      </Link>
    )
  }

  if (kind === 'content') {
    const to = `/content/${name}`
    return (
      <Link
        className='homeCard homeCard__nooverflow'
        to={to}
        key={name}
        data-voicecontrol={title}
      >
        {icon}
        <span>{title}</span>
      </Link>
    )
  }
}

const SLEEP_SEC = 1800

const calculateLimit = ({ screenMode, scale, isTooHight, isAd }) => {
  if (
    screenMode === 'wide' &&
    (scale === 1 || scale === 1.5) &&
    !isAd
  )
    return 18
  if (
    screenMode === 'wide' &&
    (scale === 1 || scale === 1.5) &&
    (isTooHight || isAd)
  )
    return 9
  if (
    ((screenMode === 'wide' && scale === 2) ||
      (screenMode === 'centered' && scale < 2)) &&
    !isTooHight &&
    !isAd
  )
    return 8
  if (
    ((screenMode === 'wide' && scale === 2) ||
      (screenMode === 'centered' && scale < 2)) &&
    (isTooHight || isAd)
  )
    return 4
  if (
    ((screenMode === 'wide' && scale === 3 && !isAd))
  )
    return 4
  if (scale === 3 && !isTooHight && !isAd) return 2
  return 2
}

export default function Home() {
  const config = useContext(ConfigurationContext)
  const { home, menu, banners, contentPath } = config

  const isLandscape = window.innerHeight < window.innerWidth

  const bannerPictures = Object.keys(banners).length
    ? Object.keys(banners)
        .map((key) =>
          banners[key].pictures.filter((p) =>
            isLandscape
              ? p.sizeName === 'Средний' && p
              : p.sizeName === 'Максимальный' && p
          )
        )
        .flat()
    : null

  const [isRedirectToScreenSaver, setRedirectToScreenServerFlag] =
    useState(false)
  const { callToAction } = home
  const screens = menu
    .filter(
      (i) =>
        i.kind === 'screen' ||
        (i.kind === 'content' && i.name !== 'deafInduction')
    )
    .filter((i) => i.enabled)

  useEffect(() => {
    const to = setTimeout(() => {
      setRedirectToScreenServerFlag(true)
    }, SLEEP_SEC * 1000)

    return () => {
      clearTimeout(to)
    }
  }, [])

  const { screenMode } = useScreenMode()
  const { scale } = useScale()
  const isTooHight = useContext(HighButtonContext)
  const blockLimit = calculateLimit({
    screenMode,
    scale,
    isTooHight,
    isAd: bannerPictures
  })
  const blocks = lodash.chunk(screens, blockLimit)

  if (isRedirectToScreenSaver) {
    return <Redirect to={{ pathname: '/screenSaver' }} />
  }

  return (
    <div className={`homeScreen ${screenMode}`}>
      <h2
        className='homeHeaderSubTitle secondaryColor'
        data-voicecontrol={callToAction}
      >
        {callToAction}
      </h2>
      <div className='homePageBlocksWrapper'>
        <div className='homePageBlocks'>
          <Slider>
            {blocks.map((block, i) => (
              <div className={`homeCardWrapper ${bannerPictures ? 'withHomeCardBanner' : ''}`} key={i}>
                {block.map((screen) => (
                  <MenuElement {...screen} key={screen.name} />
                ))}
              </div>
            ))}
          </Slider>
        </div>
        {(!isTooHight || isLandscape) && bannerPictures && (
          <div className='homePageBlocks'>
            <Slider>
              {bannerPictures.map((p, i) => (
                <div className='homeCardWrapper homeCardBanner' key={i}>
                  <img src={`${contentPath}\\${p.name}`} alt={p.name} key={i} />
                </div>
              ))}
            </Slider>
          </div>
        )}
      </div>
    </div>
  )
}
