import React, { useContext } from 'react' 
import classNames from 'classnames'

import { url } from '../func'

import {
  ConfigurationContext,
  ModifiersContext, 
} from '../contexts'

export default function Background() {
  const { backgroundPicture, contentPath } = useContext(ConfigurationContext)
  const { modifiers } = useContext(ModifiersContext)
  const { colors } = modifiers

  let bgStyle
  if (backgroundPicture && backgroundPicture.name && !colors.isContrastMode) {
    bgStyle = {
      backgroundImage: url(contentPath, backgroundPicture.name),
    }
  } else {
    bgStyle = {
      backgroundColor: colors.primaryColor
    }
  }
  return (
    <div className={classNames('homeBackground', 'magnifier-container')} style={bgStyle}/>
  )
}