import React, { useEffect, useState } from 'react'
import classNames from 'classnames'

import { ArrowShift, CursorDelete } from './svg/keyboardIcons'
import './Keyboard.css'

function Key({ item }) {
  if (item === 'shift' || item === 'shiftUp') {
    const ttsMessage = `${item === 'shiftUp' ? `Выключена` : `Включена `} печать заглавными буквами`

    return (
      <span
        className='keyboard_key arrowShift'
        data-voicecontrol={ttsMessage}
      >
        <ArrowShift />
      </span>
    )
  }
  if (item === 'EN') {
    return (
      <span
        className='keyboard_key keyboard_key__orange'
        data-voicecontrol='Английский'
      >
        {item}
      </span>
    )
  }
  if (item === 'RU') {
    return (
      <span
        className='keyboard_key keyboard_key__orange'
        data-voicecontrol='Русский'
      >
        {item}
      </span>
    )
  }
  if (item === '123') {
    return (
      <span
        className='keyboard_key keyboard_key__orange'
        data-voicecontrol='Цифровая клавиатура'
      >
        {item}
      </span>
    )
  }
  if (item === 'абв' || item === 'abc') {
    return (
      <span
        className='keyboard_key keyboard_key__orange'
        data-voicecontrol='Буквенная клавиатура'
      >
        {item}
      </span>
    )
  }

  return (
    <span className='keyboard_key' data-voicecontrol={item}>
      {item}
    </span>
  )
}

function KeyboardLayout({ keyboard, noSpace = false, clearText, type }) {
  if (typeof keyboard[0] === 'string') {
    return (
      <div className='keyboardTop'>
        {keyboard.map((item) => {
          return <Key key={item} item={item} />
        })}
        <div
          className='reviewCursor'
          onClick={clearText}
          data-voicecontrol='Удалить последний символ'
        >
          <CursorDelete />
        </div>
      </div>
    )
  }
  return (
    <>
      <div className='keyboardTop'>
        {keyboard[0].map((item) => {
          if (item === 'абв' && type === 'en') {
            item = 'abc'
          }
          return <Key key={item} item={item} />
        })}
        <div
          className='reviewCursor'
          onClick={clearText}
          data-voicecontrol='Удалить последний символ'
        >
          <CursorDelete />
        </div>
      </div>
      <div className='keyboardMiddle'>
        {keyboard[1].map((item) => (
          <Key key={item} item={item} />
        ))}
      </div>
      <div className='keyboardButtom'>
        {keyboard[2].map((item) => (
          <Key key={item} item={item} />
        ))}
      </div>
      {!noSpace && (
        <div>
          <span className='keyboardSpace' data-voicecontrol='Пробел'>
            space
          </span>
        </div>
      )}
    </>
  )
}

export default function Keyboard({
  textInput,
  changeText,
  clearText,
  onKeyPress,
  onEnter,
  style,
  isKeyboardForPhone
}) {
  const [text, setText] = useState('')
  const [shiftText, setShiftText] = useState(false)
  const [isNumKeyboard, setNumKeyboardFlag] = useState(false)
  const [keyboardType, setKeyboardType] = useState('ru')

  if (textInput || textInput === '') {
    if (textInput !== text) {
      setText(textInput)
    }
  }

  const myInput = (e) => {
    const target = e.target
    if (!target.closest('a') && !target.closest('span')) {
      return
    }

    const keyboardSimbol = target.textContent
    let val = shiftText ? keyboardSimbol.toUpperCase() : keyboardSimbol
    if (val === 'space' || val === 'SPACE') {
      val = ' '
    }
    switch (keyboardSimbol) {
      case '123':
        // Раскладка на цифры
        setNumKeyboardFlag(true)
        return

      case 'абв':
      case 'abc':
        // Обратно на клавиатуру
        setNumKeyboardFlag(false)
        return

      case 'EN':
        setKeyboardType('en')
        return

      case 'RU':
        setKeyboardType('ru')
        return

      case '':
        // Клавиша shift
        setShiftText((s) => !s)
        return
      default: {
        // Передаем данные в инпут выше родителю
        if (onKeyPress) onKeyPress(val)

        if (changeText) changeText(text + val)
        setText(text + val)
      }
    }
  }

  const keyboardShift = shiftText ? 'shiftUp' : 'shift'

  const keyboardRU = [
    ['ё', 'й', 'ц', 'у', 'к', 'е', 'н', 'г', 'ш', 'щ', 'з', 'х', 'ъ'],
    ['123', 'ф', 'ы', 'в', 'а', 'п', 'р', 'о', 'л', 'д', 'ж', 'э', 'EN'],
    [keyboardShift, 'я', 'ч', 'с', 'м', 'и', 'т', 'ь', 'б', 'ю', '.', ',']
  ]

  const keyboardEn = [
    ['q', 'w', 'e', 'r', 't', 'y', 'u', 'i', 'o', 'p'],
    ['123', 'a', 's', 'd', 'f', 'g', 'h', 'j', 'k', 'l', 'RU'],
    [keyboardShift, 'z', 'x', 'c', 'v', 'b', 'n', 'm', '.', ',']
  ]

  const keyboardNumbers = [
    ['абв', '1', '2', '3', '4', '5', '6', '7', '8', '9', '0'],
    ['+', '-', '/', ':', ';', '(', ')', '@', '”', '’'],
    ['*', '~', '<', '>', '№', '%', '?', '!']
  ]

  const keyboardForPhone = ['1', '2', '3', '4', '5', '6', '7', '8', '9', '0']

  const cl = classNames({
    keyboard: true,
    keyboard__shift: shiftText,
    keyboard__numbers: isNumKeyboard
  })

  return (
    <div className='keyboardWrapper' style={style ? style : {}}>
      <div className={cl} onClick={myInput}>
        {onEnter && (
          <div
            className='webviewEnterButton'
            onClick={onEnter}
            data-voicecontrol='Ввод'
          >
            Ввод
          </div>
        )}
        {isKeyboardForPhone && (
          <KeyboardLayout
            keyboard={keyboardForPhone}
            noSpace
            clearText={clearText}
          />
        )}{' '}
        {!isKeyboardForPhone && isNumKeyboard && (
          <KeyboardLayout
            keyboard={keyboardNumbers}
            type={keyboardType}
            // noSpace
            clearText={clearText}
          />
        )}
        {!isKeyboardForPhone && !isNumKeyboard && keyboardType === 'ru' && (
          <KeyboardLayout keyboard={keyboardRU} clearText={clearText} />
        )}
        {!isKeyboardForPhone && !isNumKeyboard && keyboardType === 'en' && (
          <KeyboardLayout keyboard={keyboardEn} clearText={clearText} />
        )}
      </div>
    </div>
  )
}
