
import React, { useState, useContext } from 'react';
import { Link, Redirect } from 'react-router-dom'

// import TopControls from "./TopControls";
import { ReviewContext } from '../contexts'
import { StarGrey, StarYellow } from "./svg/reviewIcons"

import './Review.css'

export default function ReviewStars() {

    // Данные из глобального state
    const { setReview, review } = useContext( ReviewContext)

    const [stars, setStars] = useState(review.rating)
    const [redirectUri, setRedirectUri] = useState('')

    // Сменить количество звезд
    const changeStars = (index) => {
        return () => {
            setStars(index)
        }
    }

    // Записал данные в глобальный state
    const changeData = () => {
        const rating = stars
        setReview({...review, rating})
    }

    //Массив звезд для отрисовки
    const starsElements = []
    for (let i = 0; i < 5; i++) {
        if (i + 1 > stars) {
            starsElements.push(<a key={i} onClick={changeStars(i + 1)}> <StarGrey/> </a>)
        } else {
            starsElements.push(<a key={i} onClick={changeStars(i + 1)}> <StarYellow/> </a>)
        }
    }

    const onGoBack = () => {
      /* Если текстовый отзыв, то идти на /review/text */
      /* Если видео -- то на */
      if (review && review.kind === 'text') {
        setRedirectUri('/review/text')
      } else {
        setRedirectUri('/review/video')
      }
    }

    if (redirectUri) {
      return <Redirect to={{ pathname: redirectUri }} />
    }

    return (
      <div className="keyboardScreenContainer">
          {/* <TopControls /> */}
          <div className="reviewWrapper">
              <h5 className="reviewTitle">2 шаг из 4</h5>
              <h4 className="reviewSubTitle">Ваша оценка</h4>

              <div className="reviewStarsWrap">
                  {starsElements}
              </div>

              <div className="ReviewButtonWrapper reviewStarsButtonsWrap">
                  <span
                    className="ReviewButton reviewButtonBack"
                    onClick={onGoBack}
                  >
                    Назад
                  </span>
                  <Link className="ReviewButton reviewButtonNext"
                        to="/review/name"
                        onClick={changeData}
                  >Далее</Link>
              </div>
          </div>
      </div>
    )

}
