import intersection from 'lodash/intersection'

const SOUND_MAP = {
  '.': 'Точка',
  ',': 'Запятая',
  '-': 'Дефис',
  '/': 'Слэш',
  ':': 'Двоеточие',
  ';': 'Точка с запятой',
  '(': 'Скобка открывается',
  ')': 'Скобка закрывается',
  '”': 'Двойные кавычки',
  '’': 'Одинарные кавычки',
  '<': 'Меньше чем',
  '?': 'Вопрос',
  '!': 'Восклицательный знак'
}

const soundEls = {}
export function playSound(soundId) {
  if (soundEls[soundId]) {
    soundEls[soundId].play()
    return
  }

  const el = document.getElementById(soundId)
  if (el) {
    soundEls[soundId] = el
    el.play()
  }
}

let vcData = {}

const BUTTON_CLASSES = [
  'ReviewButton',
  'button',
  'homeCard',
  'keyboard_key',
  'operatorChatFinishBtn ',
  'reviewButtonBig',
  'operatorChatArrowTop',
  'operatorChatArrowBottom'
]

const SOUND_CLASSES = [
  'reviewTitle',
  'reviewSubTitle',
  'reviewText',
  'reviewButtonBig',
  'ReviewButton',

  'keyboard_key',
  'button',
  'homeCard',
  'operatorChatFinishBtn ',
  'reviewPhoneComment',

  'reviewFinishMessage',
  'operatorChatConnectionTitle',

  'message_text',
  'operatorChatClientTitle',
  'operatorChatOperatorTitle'
]

const TAG_NAMES = ['A', 'SPAN', 'DIV', 'BUTTON', 'BODY']

function findNodeOrParentByClassList(el, classList) {
  let i = el
  while (i) {
    const commonClasses = intersection(el.classList, classList)

    if (commonClasses.length > 0) {
      return el
    }

    i = i.parentNode
  }

  return null
}

function findNearVoiceControl(el) {
  let i = el
  while (i) {
    if (i && i.getAttribute) {
      const vc = i.getAttribute('data-voicecontrol')
      if (vc) {
        return vc
      }
    }

    i = i.parentNode
  }

  return null
}

let isInited = false

const krustSynth = window.speechSynthesis

export function textToSpeech(e) {
  let target = e.target

  let vc = findNearVoiceControl(target) //.getAttribute('data-voicecontrol')
  if (!vc) {
    const el = findNodeOrParentByClassList(target, SOUND_CLASSES)
    if (el && el.textContent) {
      vc = el.textContent
    }
  }

  if (SOUND_MAP[vc]) {
    vc = SOUND_MAP[vc]
  }

  if (vc && window.krustVCIsEnabled) {
    if (!vcData[vc]) {
      vcData[vc] = new SpeechSynthesisUtterance(vc)
    }

    krustSynth.cancel()
    krustSynth.speak(vcData[vc])
    return
  }

  while (TAG_NAMES.indexOf(target.tagName) === -1) {
    target = target.parentNode
  }

  const className = target.className
  if (!className || !className.indexOf) {
    return
  }

  for (const buttonClass of BUTTON_CLASSES) {
    if (className.indexOf(buttonClass) >= -1) {
      playSound('sound1')
      return
    }
  }
}

export function initButtonsSound() {
  if (isInited) {
    return
  }

  document.body.addEventListener('click', textToSpeech)

  isInited = true
}
