import './header.css'
import React, { useContext } from 'react'
import { useLocation } from 'react-router-dom'
import { ConfigurationContext } from '../../contexts'
import LogoAndTitle from './LogoAndTitle'
import BackAndMain from './BackAndMain'
import ControlPanel from './ControlPanel'

export default function Header() {
  const { title, logo, menu, contentPath } = useContext(ConfigurationContext)
  const controls = menu.filter(
    (i) =>
      i.kind === 'control' ||
      (i.kind === 'content' && i.name === 'deafInduction')
  )

  const { pathname } = useLocation()
  const isScreenSaverPage = pathname.includes('screenSaver')

  if (isScreenSaverPage) return null

  return (
    <header className='mainHeader'>
      <BackAndMain />
      <LogoAndTitle contentPath={contentPath} title={title} logo={logo} />
      <ControlPanel controls={controls} />
    </header>
  )
}
