import React, { useRef, useState, useEffect } from 'react'
import Keyboard from './Keyboard'
import { activateLicense, connectServer, pingServer } from '../api'
import SetupVideoCams from './SetupVideoCameras'
import './StartConfig.css'
import './License.css'
import { ipcRenderer } from 'electron'
import { licenseStatuses } from '../constants'

export default function License({ licenseCodeStrId, licenseStatus, onLicenseCodeStrIdChange, onLicenseStatusChange }) {
  const licenseInputRef = useRef(null)
  const formRef = useRef(null)
  const [currentField, setCurrentField] = useState(licenseInputRef)
  const [currentText, setCurrentText] = useState('')
  const [activationError, setActivationError] = useState(null)
  const [requestInProgress, setRequestInProgress] = useState(false)
  const [isNetworkConnectionError, setIsNetworkConnectionError] = useState(false)

  useEffect(() => {    
    if (licenseStatus !== licenseStatuses.unknown) {
      setActivationError(licenseStatus)
    }    
    
    if (licenseCodeStrId) {
      setCurrentText(licenseCodeStrId)
      currentField.current.value = licenseCodeStrId
    }
  }, [licenseStatus, licenseCodeStrId])

  useEffect(() => {
    pingServer().then((res) => {
      if (res.message && res.message.includes('network is offline')) {
        setIsNetworkConnectionError(true)
      }
    })
  }, [])

  const getErrorTranslation = (error) => {
    let errorTranslation;

    console.log('error', error)

    switch (error) {
      case 'activation code does not exist':
        errorTranslation = 'Введенный лицензионный код не найден (Test Warez)'
        break;
      case 'maximum activations exceeded':
        errorTranslation = 'Превышен лимит активаций (Test Warez)'
        break;
      case 'no response':
        errorTranslation = 'Ошибка выполнения запроса (Test Warez)'
        break;
      case 'rejected':
        errorTranslation = 'Лицензионный код отозван (Test Warez)'
        break;
      case 'empty code':
        errorTranslation = 'Поле не может быть пустым (Test Warez)'
        break;
      case 'activation code is disabled':
        errorTranslation = 'Лицензионный код отозван (Test Warez)'
        break;
      case 'offline code is invalid':
        errorTranslation = 'Неверный код для оффлайн-активации (Test Warez)'
        break;
      default:
        errorTranslation = 'Неизвестная ошибка (Test Warez)'
    }

    if (typeof error === 'string') {
      if (error.toLowerCase().includes('request has been terminated')) {
        errorTranslation = 'Ошибка подключения'
      }
    }

    return errorTranslation;
  }

  const onChangeText = (text) => {
    setActivationError(null)
    setCurrentText(text)
    currentField.current.value += text[text.length - 1]
  }

  const clearText = () => {
    setActivationError(null)
    setCurrentText(currentText.slice(0, -1))
    currentField.current.value = currentField.current.value.slice(0, -1)
  }

  const onInputClick = (ref) => {
    if (!ref) 
      return;

    ref.current.focus()

    setActivationError(null)
    setCurrentField(ref)
  }

  const onSavePress = async (e) => {
    if (!formRef.current) 
        return

    const licenseCode = formRef.current.licenseKey.value

    if (!licenseCode) {
      setActivationError('empty code')
      return;
    }

    //const isValid1 = ipcRenderer.sendSync('ensure-offline-license-is-valid')
//
    //console.log('isValid', isValid)

    setRequestInProgress(true)

    const activationResult = await activateLicense(licenseCode)

    setRequestInProgress(false)

    if (activationResult.errorMessage) { 
      if (activationResult.errorMessage.includes('the network is offline')) {
        setIsNetworkConnectionError(true)

        console.log('the network is offline, activation with license key')

        ipcRenderer.sendSync('save-license-key', licenseCode)
        const isSavedKeyValid = ipcRenderer.sendSync('ensure-license-key-is-valid')

        if (!isSavedKeyValid) {
          setActivationError('offline code is invalid')
        }
        else {
          onLicenseStatusChange(licenseStatuses.valid)
        }
      }
      else {
        setIsNetworkConnectionError(false)
        setActivationError(activationResult.errorMessage)
      }

      return;
    }  

    if (!activationResult.licenseKey) {
      throw new Error('License had no licenseKey!')
    }

    //save licenseKey etc
    ipcRenderer.sendSync('save-license-key', activationResult.licenseKey);
    //ipcRenderer.sendSync('save-license-code', licenseCode)

    const isValid = ipcRenderer.sendSync('ensure-license-key-is-valid')

    if (isValid) {
      const softwareVersion = ipcRenderer.sendSync('get-software-version')    

      pingServer(softwareVersion, licenseCode)
    }

    onLicenseCodeStrIdChange(licenseCode)
    onLicenseStatusChange(isValid ? licenseStatuses.valid : licenseStatuses.invalid)
  }

  const machineId = ipcRenderer.sendSync('get-system-token')

  const inputPlaceholder = isNetworkConnectionError ? 'Введите код для оффлайн-активации' : 'Введите код лицензии'

  const inputClassname = activationError ? 'errorInput' : ''

  return (
    <section className='startConfig'>      
      <form ref={formRef} className='startConfig_form'>
        {
          isNetworkConnectionError &&
          <div className='connectionErrorContainer'>
            <p>Не удалось установить соединение с сервером, используйте код для оффлайн-активации. <br />Идентификатор устройства: <b>{machineId}</b></p>
          </div>
        }         
        {
          (requestInProgress && !activationError) &&
          <span className='connectionInProgress'>Идет выполнение запроса...</span>
        }
        {
          activationError &&
          <span className='activationError'>{getErrorTranslation(activationError)}</span>
        }       
        <input
          type='text'
          name='licenseKey'
          placeholder={inputPlaceholder}
          ref={licenseInputRef}          
          onClick={() => onInputClick(licenseInputRef)}
          required
          autoFocus
          className={inputClassname}
        />                
      </form>      
      <div className='startConfig_KeyboardWrap'>
        <Keyboard
          changeText={onChangeText}
          textInput={currentField}
          clearText={clearText}
        />
        <button className='startConfig_submit' onClick={onSavePress}>
          Соjjjjхранить
        </button>
      </div>
    </section>
  )
}