import React, { useRef, useContext, useEffect } from 'react'
import nanoid from 'nanoid'

import { postCreateMessage } from '../api'
import { SocketContext } from '../contexts'

import {
  SpeechMask,
  SpeechButton,
  ChatDecorationWhite,
  ChatDecorationIcon
} from './svg/chatIcons'
import Keyboard from './Keyboard'
import './OperatorChat.css'
import { ContentLeft, ContentRight } from './svg/contentArrows'

import { playSound } from '../sound'

function ChatMessage({ message }) {
  const voiceChatMessage = () => {
    const synth = window.speechSynthesis
    // const voices = synth.getVoices()
    const text = new SpeechSynthesisUtterance(message.text)
    synth.speak(text)
  }
  if (!message.userName) {
    return (
      <div className='operatorChatClientText'>
        <span className='operatorChatClientTitle'>Вы</span>
        <div className='message_text'>{message.text}</div>
        <div className='chatDecorationBlock'>
          <ChatDecorationIcon />
        </div>
      </div>
    )
  }

  return (
    <div className='operatorChatOperatorWrap'>
      <div className='operatorChatOperatorText'>
        <span className='operatorChatOperatorTitle'>{message.userName}</span>
        <div className='message_text'>{message.text}</div>
      </div>
      <div className='operatorChatOperatorTextBg'>
        <div className='speechMaskWrap'>
          <SpeechMask />
        </div>
        <div className='speechButton' onClick={voiceChatMessage}>
          <SpeechButton />
        </div>
      </div>
    </div>
  )
}

export default function OperatorChat({
  chat,
  textInput,
  setTextInput,
  messages,
  setMessages
}) {
  const scrollEl = useRef(null)
  // const entry = useRef(null)

  // long text debug
  window.st = setTextInput

  const socket = useContext(SocketContext)

  const scrollToBottom = () => {
    if (!scrollEl.current) {
      return
    }

    console.log('scroll to bottom', scrollEl.current.childNodes[0].clientHeight)
    scrollEl.current.scrollTop = scrollEl.current.childNodes[0].clientHeight
  }

  const scrollStepUp = () => {
    if (!scrollEl.current) {
      return
    }

    scrollEl.current.scrollTop -= 30
  }

  const scrollStepDown = () => {
    if (!scrollEl.current) {
      return
    }

    scrollEl.current.scrollTop += 30
  }

  const onMessage = ({ message }) => {
    if (message.chatStrId === chat.strId) {
      setMessages((messages) => [...messages, message])
      scrollToBottom()
      playSound('sound2')
    }
  }

  useEffect(() => {
    if (!socket || !chat.strId) {
      return
    }

    socket.on('textMessage', onMessage)

    return () => {
      socket.removeListener('textMessage', onMessage)
    }
  }, [chat, socket]) //chat каким-то образом меняется с true на объект чата :/

  // Добавляем текст, что писал пользователь
  const addText = () => {
    if (!textInput) {
      return
    }

    console.log('creatingMessage', chat.strId)

    const message = {
      strId: nanoid(),
      chatStrId: chat.strId,
      text: textInput
    }
    // sendMessage(textInput)
    postCreateMessage(message)

    const newArr = [...messages, message]
    setMessages(newArr)

    setTextInput('')

    // нужно время, чтобы экран обновился после установки стейта
    // я не знаю простого способа сделать это декларативно
    setTimeout(scrollToBottom, 10)
  }
  // Текст что приходит из клавиатуры
  const changeText = (text) => {
    setTextInput(text)
  }

  // Очистка текста
  const clearText = () => {
    setTextInput(textInput.slice(0, -1))
  }

  const isLandscape = window.innerHeight < window.innerWidth

  return (
    <>
      <div className='operatorChatInfoWrap'>
        <div ref={scrollEl} className='b_messages'>
          <div className='b_messages_scroll'>
            {messages.map((message) => (
              <ChatMessage key={message.strId} message={message} />
            ))}
            {!isLandscape && (
              <div className='operatorChatClientEntry'>
                <div className='message_text'>
                  {textInput ? (
                    textInput
                  ) : (
                    <span className='operatorChatPlaceholder'>
                      Написать сообщение...
                    </span>
                  )}
                </div>
                <div className='chatDecorationBlock'>
                  <ChatDecorationWhite />
                </div>
              </div>
            )}
          </div>
        </div>
        <div
          className='operatorChatArrowTop'
          onClick={scrollStepUp}
          data-voicecontrol='Прокрутка сообщений вверх'
        >
          <ContentLeft />
        </div>
        <div
          className='operatorChatArrowBottom'
          onClick={scrollStepDown}
          data-voicecontrol='Прокрутка сообщений вниз'
        >
          <ContentRight />
        </div>
      </div>
      <div className='operatorChatKeyboardWrap'>
        {isLandscape && (
          <div className='operatorChatClientEntry'>
            <div className='message_text'>
              {textInput ? (
                textInput
              ) : (
                <span className='operatorChatPlaceholder'>
                  Написать сообщение...
                </span>
              )}
            </div>
          </div>
        )}
        <Keyboard
          changeText={changeText}
          textInput={textInput}
          clearText={clearText}
        />
        <div
          className='operatorChatClientBtn'
          onClick={addText}
          data-voicecontrol='Ответить'
        >
          Ответить
        </div>
      </div>
    </>
  )
}
