export async function getMediaDeviceIds() {
  const devices = await navigator.mediaDevices.enumerateDevices();
  const cams = devices.filter(element => element.kind === 'videoinput');
  return cams.map(c => c.deviceId);
}

export async function getVideoStream(deviceId, audio = true) {
  return await navigator.mediaDevices.getUserMedia({
    audio,
    video: { deviceId }
  })
}

export function setVideoStream(video, stream, play = true) {
  video.srcObject = stream;
  video.onloadedmetadata = (e) => {
    console.log('setVideoStream@onloadmetadata');
    play && video.play();
  };
}
