import { ipcRenderer } from 'electron'
import React, { useCallback, useContext, useEffect, useState } from 'react'
import {
  ConfigurationContext,
  ModifiersContext,
  defaultModifiers,
  applyColors
} from '../contexts'
import { applyForAll } from '../iframeHelpers'

import { SelectorUp, SelectorDown } from './svg/selectorIcons'
// import { join } from '../func'

const filterNames = [
  "None", 
  "Sepia",
  "GrayScale",
  "HueShift180",
  "Negative", 
  "NegativeGrayScale",
  "NegativeHueShift180",
  "NegativeHueShift180Variation1",
  "NegativeHueShift180Variation2",
  "NegativeHueShift180Variation3",
  "NegativeHueShift180Variation4",
  "NegativeRed", 
  "NegativeSepia",     
]

export default function ContrastSelector({ topClassContrast }) {
  const { a11yColors, filters } = useContext(ConfigurationContext)
  const { modifiers, setModifiers } = useContext(ModifiersContext)  
  const { colors } = modifiers
  const { selectedContrastPair, selectedFilterName } = colors
  const [i, setI] = useState(selectedContrastPair)
  const [availableFilters, setAvailableFilters] = useState(filterNames) 

  const hasMore = availableFilters.indexOf(selectedFilterName) < availableFilters.length - 1
  const hasLess = availableFilters.indexOf(selectedFilterName) > 0

  const applyFilter = (filterName) => {
    ipcRenderer.sendSync('apply-color-filter', filterName);
    updateModifiers(filterName)
  }

  useEffect(() => {
    if (!filters)
      return;

    const newFilters = ['None']

    Object.getOwnPropertyNames(filters).forEach((filterName) => {
      if (filters[filterName]) //if filter is enabled, show it menu
        newFilters.push(filterName)
    })

    setAvailableFilters(newFilters)
  }, [filters])

  const applyNextFilter = useCallback((e) => {
    e.stopPropagation();

    const currentFilterIndex = availableFilters.findIndex((it) => selectedFilterName === it)

    if (currentFilterIndex !== availableFilters.length - 1)
      applyFilter(availableFilters[currentFilterIndex + 1])
  }, [availableFilters, selectedFilterName])

  const applyPreviousFilter = useCallback((e) => {
    e.stopPropagation();

    const currentFilterIndex = availableFilters.findIndex((it) => selectedFilterName === it)

    if (currentFilterIndex !== 0)
      applyFilter(availableFilters[currentFilterIndex - 1])
  }, [availableFilters, selectedFilterName])

  const updateModifiers = useCallback((selectedFilterName) => {
    setModifiers({...modifiers, colors: { ...modifiers.colors, selectedFilterName: selectedFilterName }})
  }, [modifiers])

  const reset = (e) => {
    e.stopPropagation()
    
    applyFilter('None')
  }

  const stylesDown = {
    visibility: hasMore ? 'visible' : 'hidden'
  }
  const stylesUp = {
    visibility: hasLess ? 'visible' : 'hidden'
  }
  // const colorPair = (i >= 0 && i < a11yColors.length) ? a11yColors[i] : null

  const styleTopControls = topClassContrast
    ? 'modifiersSelector topControlsModifiersSelectorContrast'
    : 'modifiersSelector'

  return (
    <div className={styleTopControls}>
      <div style={stylesUp} onClick={applyPreviousFilter}>
        <SelectorUp />
      </div>
      <div className='modifiersSelector_colors'>
        {i === -1 && <div className='modifiersSelector_whiteSq' />}
        {false && i > -1 && <div className='modifiersSelector_chameleon' />}
        {i > -1 && <div className='modifiersSelector_filter'>{selectedFilterName}</div>}
      </div>
      <div style={stylesDown} onClick={applyNextFilter}>
        <SelectorDown />
      </div>
      <div className='modifiersSelector_reset' onClick={reset}>
        Сбросить
      </div>
    </div>
  )
}
