import React from 'react'

export const ConfigurationContext = React.createContext(null)
export const ReviewContext = React.createContext(null)
export const GlobalControls = React.createContext(null)
// screen mode
export const ScreenModeContext = React.createContext(null)
export const useScreenMode = () => React.useContext(ScreenModeContext)
export const defaultScreenMode = 'wide'
// scale
export const ScaleContext = React.createContext(null)
export const useScale = () => React.useContext(ScaleContext)
export let currentScale = { koeff: 1 }

export const TemperatureContext = React.createContext(null)

let currentColors = {
  primaryColor: '#060606',
  secondaryColor: '#FFFFFF',
  isContrastMode: false
}

export const defaultReview = {
  text: '',
  rating: 1,
  userName: '',
  phone: '',
  kind: 'text',
  blob: null
}

export const defaultModifiers = {
  colors: {
    primaryColor: '#060606',
    secondaryColor: '#FFFFFF',
    isContrastMode: false,
    selectedContrastPair: -1,
    selectedFilterName: 'None'
  }
}

export const ModifiersContext = React.createContext(defaultModifiers)
export const SocketContext = React.createContext(null)
export const HighButtonContext = React.createContext(null)
export const ExternalWebviewKeyboardContext = React.createContext(null)

export function getColors() {
  return currentColors
}

export function getScale() {
  return currentScale
}

export function applyColors(colors) {
  currentColors = colors
}

export function applyScale(scale) {
  currentScale = scale
}
