import React, { useState, useContext } from 'react'
import { Link } from 'react-router-dom'
import { Cancel } from '../svg/selectorIcons'
import { homeIconList } from '../svg/homeIcons'
import { GlobalControls, useScreenMode } from '../../contexts'
import ContrastSelector from '../ContrastSelector'
import ScaleSelector from '../ScaleSelector'

function ControlItem({ kind, name, title }) {
  const { setMagnifier, isMagnifier, voiceInductionToggle, isVoiceEnabled } =
    useContext(GlobalControls)
  const [isOpened, setOpened] = useState(getDefaultValue())
  const Icon = homeIconList[name]

  const icon = Icon ? (
    <span data-voicecontrol={title}>
      <Icon />
    </span>
  ) : null

  const open = () => {
    setOpened(!isOpened)
  }

  function getDefaultValue() {
    if (name === 'voiceInduction') {
      return window.krustVCIsEnabled
    }
    return false
  }

  if (kind === 'content') {
    const to = `/content/${name}`
    return (
      <Link
        className='deafInduction'
        to={to}
        key={name}
        data-voicecontrol={title}
      >
        {icon}
      </Link>
    )
  }

  switch (name) {
    case 'screenMagnifier':
      return (
        <button
          className={`${isMagnifier ? 'opened' : ''}`}
          onClick={() => setMagnifier(!isMagnifier)}
          data-voicecontrol={isMagnifier ? 'Закрыть' : title}
        >
          {isMagnifier ? (
            <span data-voicecontrol='Закрыть'>
              <Cancel />
            </span>
          ) : (
            <span data-voicecontrol={title}>
              <Icon />
            </span>
          )}
        </button>
      )
    case 'fonts':
    case 'a11yColors':
      return (
        <button
          className={`${isOpened ? 'opened' : ''}`}
          data-voicecontrol={isOpened ? 'Закрыть' : title}
          onClick={open}
        >
          {isOpened ? (
            <span data-voicecontrol='Закрыть'>
              <Cancel />
            </span>
          ) : (
            <span data-voicecontrol={title}>
              <Icon />
            </span>
          )}
          {isOpened && name === 'fonts' && <ScaleSelector />}
          {isOpened && name === 'a11yColors' && <ContrastSelector />}
        </button>
      )
    case 'voiceInduction':
      return (
        <button
          className={`${isVoiceEnabled ? 'opened' : ''}`}
          data-voicecontrol={isVoiceEnabled ? 'Закрыть' : title}
          onClick={() => {
            voiceInductionToggle()
            window.krustVCIsEnabled = !window.krustVCIsEnabled
            const synth = window.speechSynthesis
            const text = new SpeechSynthesisUtterance('Озвучивание при нажатии')
            synth.speak(text)
          }}
        >
          {isVoiceEnabled ? (
            <span data-voicecontrol='Закрыть'>
              <Cancel />
            </span>
          ) : (
            <span data-voicecontrol={title}>
              <Icon />
            </span>
          )}
        </button>
      )
    default:
      return <button data-voicecontrol={title}>{icon}</button>
  }
}

function ControlPanel({ controls }) {
  const { screenMode, setScreenMode } = useScreenMode()

  const onClick = () => {
    if (screenMode === 'wide') {
      setScreenMode('centered')
    } else {
      setScreenMode('wide')
    }
  }

  const { Wide, Centered } = homeIconList

  return (
    <div className='controlPanel'>
      <button onClick={onClick} data-voicecontrol='Режим экрана'>
        <span>{screenMode === 'wide' ? <Centered /> : <Wide />}</span>
      </button>
      {controls.map((c) => c.enabled && <ControlItem key={c.name} {...c} />)}
    </div>
  )
}

export default ControlPanel
