import React, {useEffect, useContext, useState} from 'react'
import { Link } from 'react-router-dom'
import { homeIconList } from './svg/homeIcons'
import { ReviewContext, defaultReview } from '../contexts'

import './Review.css'

export default function Review() {
  const [webcam, setWebcam] = useState(true)
  const { setReview } = useContext(ReviewContext)
  const { BigReview } = homeIconList

  useEffect(() => {
    setReview(defaultReview)

    let md = navigator.mediaDevices;
    if (!md || !md.enumerateDevices) return false;
    md.enumerateDevices().then(devices => {
      console.log('devices', devices)
      setWebcam(devices.some(device => 'videoinput' === device.kind))
    })
  }, [])

  return (
    <>
      <div className='SelectReviewWrapper'>
        <BigReview />
        <h3 className='actionHeader'>
          Выберите способ <br /> обратной связи о клинике
        </h3>
        <div className='ReviewButtonWrapper'>
          { webcam &&
              <Link className='ReviewButton' to='/review/video'>
                Видеоотзыв
              </Link>
          }
          <Link className='ReviewButton' to='/review/text'>
            Текстовый отзыв
          </Link>
        </div>
      </div>
    </>
  )
}
