import React, { useRef, useState, useEffect } from 'react'
import Keyboard from './Keyboard'
import { connectServer } from '../api'
import SetupVideoCams from './SetupVideoCameras'
import './StartConfig.css'

const StartConfigPage = ({ setRootConfig, setConfigData }) => {
  const serverRef = useRef(null)
  const terminalNameRef = useRef(null)
  const formRef = useRef(null)
  const [serverConnectionProccess, setServerConnection] = useState(false)
  const [error, setError] = useState(false)
  const [serverError, setServerError] = useState(false)
  const [currentField, setCurrentField] = useState(serverRef)
  const [currentText, setCurrentText] = useState('')
  const [shouldSetCams, setCams] = useState(false)
  const [startConfigInfo, setStartConfigInfo] = useState({})

  useEffect(() => {
    if (serverConnectionProccess) {
      setServerError(false)
    }
  }, [serverConnectionProccess])

  const onChangeText = (text) => {
    setCurrentText(text)
    currentField.current.value += text[text.length - 1]
    if (error) setError(false)
    if (serverError) setServerError(false)
  }

  const clearText = () => {
    setCurrentText(currentText.slice(0, -1))
    currentField.current.value = currentField.current.value.slice(0, -1)
  }

  const onClickInput = (ref) => {
    if (!ref) 
      return;

    ref.current.focus()
    setCurrentField(ref)
    if (serverError) setServerError(false)
    if (error) setError(false)
  }

  const saveData = (e) => {
    if (!formRef.current) return

    const serverHost = formRef.current.serverHost.value || 'localhost:8010'
    const name = formRef.current.terminalName.value || 'AsyaCams'

    if (!serverHost || !name) {
      setError(true)
      return
    }

    setServerConnection(true)
    connectServer(serverHost, name)
      .then((res) => {
        setStartConfigInfo(res)
        setCams(true)
      })
      .catch(() => {
        setServerConnection(false)
        setServerError(true)
      })
  }

  if (shouldSetCams) {
    return (
      <SetupVideoCams
        config={startConfigInfo}
        setConfigData={setConfigData}
        setRootConfig={setRootConfig}
      />
    )
  }

  return (
    <section className='startConfig'>
      <form ref={formRef} className='startConfig_form'>
        <input
          type='text'
          name='serverHost'
          placeholder='Введите адрес сервера'
          ref={serverRef}
          onClick={() => onClickInput(serverRef)}
          required
          autoFocus
        />
        <input
          type='text'
          name='terminalName'
          placeholder='Введите название терминала'
          ref={terminalNameRef}
          onClick={() => onClickInput(terminalNameRef)}
          required
        />
        {error && (
          <span className='startConfig_error'>
            Необходимо заполнить все поля!
          </span>
        )}
        {serverError && (
          <span className='startConfig_serverError'>Сервер не существует!</span>
        )}
        {serverConnectionProccess && (
          <span className='startConfig_serverConnection'>
            Идет подключение к серверу. Пожалуйста, подождите...
          </span>
        )}
      </form>
      <div className='startConfig_KeyboardWrap'>
        <Keyboard
          changeText={onChangeText}
          textInput={currentField}
          clearText={clearText}
        />
        <button className='startConfig_submit' onClick={saveData}>
          Сохранить данные
        </button>
      </div>
    </section>
  )
}

export default StartConfigPage
