import React, {
  useState,
} from 'react'
import { spawn } from 'child_process'
import path from 'path'
import killTree from 'tree-kill'

const EXECUTABLE_PATH = path.resolve('./mag/Magnification.exe')


let bin = null

function kill(cb) {
  if (!bin) {
    return cb()
  }
  killTree(bin.pid, () => {
    bin = null
    cb()
  })
}

function makeBin({ position }, onClose) {
  bin = spawn(EXECUTABLE_PATH, [position])
  bin.on('error', onClose)
  bin.on('exit', onClose)
}

export function useMagnification() {
  const [isOpen, setIsOpen] = useState(false)
  const close = () => setIsOpen(false)
  const open = () => setIsOpen(true)

  const toggle = (isTooHigh) => {
    if (isOpen) {
      kill(close)
    } else {
      makeBin({
        position: isTooHigh ? 'center' : 'bottom-center'
      }, close)
      open()
    }
  }

  return [isOpen, toggle]
}
