import path from 'path'
import fs from 'fs'
import { promisify } from 'util'
import { postVideoChat } from '../api'

const asyncWriteFile = promisify(fs.writeFile);
const asyncUnlink = promisify(fs.unlink);
const asyncRmdir = promisify(fs.rmdir);

export default async function(data, operator, durationInMilliseconds, terminalName) {
  const blob = new Blob(data);
  const buffer = Buffer.from(await blob.arrayBuffer());

  const dir = path.join(process.cwd(), 'videoChat');
  if (!fs.existsSync(dir)){
    fs.mkdirSync(dir)
  }
  const filePath = path.join(dir, `vid-${Date.now()}.webm`);

  asyncWriteFile(filePath, buffer)
    .then(() => postVideoChat(blob, operator, durationInMilliseconds / 1000, terminalName))
    .then(() => asyncUnlink(filePath))
    .then(() => asyncRmdir(dir))
    .catch(e => console.error('ERROR with video file', e.message))
}