import React, { useState, useEffect, Children } from 'react'
import { homeIconList } from '../svg/homeIcons'
import './slider.css'

function Dots({ screenCount, currentScreen }) {
  return (
    <div className='dots'>
      {Array.from({ length: screenCount + 1 }).map((_, i) => (
        <div
          className={`dot ${i === currentScreen ? 'current' : ''}`}
          key={i}
        ></div>
      ))}
    </div>
  )
}

function Buttons({ screenCount, currentScreen, setCurrentScreen }) {
  const { ArrowLeft, ArrowRight } = homeIconList
  const previousScreen = () => {
    if (currentScreen === 0) return
    const nextScreen = currentScreen - 1
    setCurrentScreen(nextScreen)
  }

  const nextScreen = () => {
    if (currentScreen === screenCount) return
    const nextScreen = currentScreen + 1
    setCurrentScreen(nextScreen)
  }

  return (
    <div className='buttons'>
      <button onClick={previousScreen} data-voicecontrol='Влево'>
        <ArrowLeft />
      </button>
      <button onClick={nextScreen} data-voicecontrol='Вправо'>
        <ArrowRight />
      </button>
    </div>
  )
}

export default function Slider({ children }) {
  const [currentScreen, setCurrentScreen] = useState(0)
  const screenCount = Children.count(children) - 1

  useEffect(() => {
    if (currentScreen > screenCount) {
      setCurrentScreen(screenCount)
    }
  }, [screenCount, currentScreen])

  if (screenCount === 0) return <>{children}</>

  return (
    <div className='slider'>
      {children[currentScreen]}
      <div className='sliderControls'>
        <div className='fake'></div>
        <Dots screenCount={screenCount} currentScreen={currentScreen} />
        <Buttons
          screenCount={screenCount}
          currentScreen={currentScreen}
          setCurrentScreen={setCurrentScreen}
        />
      </div>
    </div>
  )
}
