import React, { useState, useContext, useEffect } from 'react'
import { Link } from 'react-router-dom'
import Keyboard from './Keyboard'
import { ReviewContext, HighButtonContext } from '../contexts'
import './Review.css'

export default function ReviewText() {
  const { setReview, review } = useContext(ReviewContext)
  const [textInput, setTextInput] = useState(review.userName)

  const isTooHight = useContext(HighButtonContext)

  useEffect(() => {
    const mainContainer = document.querySelector('.mainContentContainer')
    if (isTooHight) {
      mainContainer.style.top = '20%'
    } else {
      mainContainer.style.top = ''
    }
    return () => {
      mainContainer.style.top = ''
    }
  }, [isTooHight])

  const changeText = (text) => {
    setTextInput(text)
  }

  const clearText = () => {
    setTextInput(textInput.slice(0, -1))
  }

  // Записал данные в глобальный state
  const changeData = () => {
    const userName = textInput ? textInput : 'Имя не заполнено'
    setReview({ ...review, userName })
  }

  return (
    <div className='keyboardScreenContainer'>
      <div className='reviewWrapper'>
        <h5 className='reviewTitle'>3 шаг из 4</h5>
        <h4 className='reviewSubTitle'>Ваше имя (необязательно)</h4>

        <div className='reviewTextWrap'>
          <div className='reviewText reviewTextName'>{textInput}</div>
        </div>
        <Keyboard changeText={changeText} textInput={textInput} clearText={clearText} />
        <div className='ReviewButtonWrapper'>
          <Link className='ReviewButton reviewButtonBack' to='/review/stars'>
            Назад
          </Link>
          <Link
            className='ReviewButton reviewButtonNext'
            to='/review/phone'
            onClick={changeData}
          >
            Далее
          </Link>
        </div>
      </div>
    </div>
  )
}
