
import React, {useContext, useEffect, useState} from 'react'
import { Link } from 'react-router-dom'
import { ReviewContext } from '../contexts'

// import TopControls from "./TopControls";

import './Review.css'
import { StarsFinish } from "./svg/reviewIcons"
import { postCreateReview } from "../api"
import Spinner from "./Spinner"

export default function ReviewFinish() {
  const { review, setReview } = useContext( ReviewContext)
  const [result, setResult] = useState(false)
  const newArr = []

  for (let i = 0; i < review.rating; i++) {
    newArr.push(i)
  }

  useEffect(() => {
    if (!review && result) {
      return
    }

    // Передаем отзыв на сервер
    postCreateReview(review)
      .catch((err) => {
        console.log("chat:create@err", err)
      })

    setResult(true)

    // Очистка отзыва от клиента
    setReview({
      text: '',
      rating: 1,
      userName: '',
      phone: '',
      blob: null,
      kind: '',
    })
  }, [])

  return (
    <>
        {/* <TopControls /> */}
        <div className="reviewFinishWrap">
          {
            result ?
          <>
            <div className="reviewFinishStarsWrap">
              {
                newArr.map( (item, index) => {
                  return (
                    <div key={index} className="reviewFinishStarsItem">
                      <StarsFinish/>
                    </div>
                  )
                })
              }
            </div>
            <div className="reviewFinishMessage"> Отзыв успешно отправлен! </div>
            <Link
              className="ReviewButton reviewFinishBtn"
              to="/"
            >К меню</Link>
          </>
          : <Spinner />
          }
        </div>
    </>
  )
}
