import React, { useState, useEffect, Fragment, useRef } from 'react'
import { AppContainer as ReactHotAppContainer } from 'react-hot-loader'

import ReactDOM from 'react-dom'
import io from 'socket.io-client'
import './styles/scale.css'
import './styles/landscape.css'
import './styles/wideScreen.css'

import Root from './components/Root'
import GlobalStyles from './components/GlobalStyles'
import {
  ConfigurationContext,
  ModifiersContext,
  defaultModifiers,
  ReviewContext,
  SocketContext,
  GlobalControls,
  defaultReview,
  TemperatureContext
} from './contexts'
import { setHost, setTerminalName, fetchUpdateInfo, activateLicense, checkLicense, pingServer, getWeather } from './api'
import StartConfigPage from './components/StartConfigPage'
import WaitForUpdate from './components/WaitForUpdate'
import { useMagnification } from './components/Magnification'

const { ipcRenderer } = require('electron')

import { initSignaling } from './webrtc'
import License from './components/License'
import { licenseStatuses } from './constants'
import CheckingLicense from './components/CheckingLicense'
import { useTemperatureSensorData } from './components/TemperatureSensor'

const AppContainer = process.env.START_HOT ? ReactHotAppContainer : Fragment

let applyNewConfiguration = null
let settingsReset = false
let currentConfig = null

ipcRenderer.on('update-installed', () => {
  console.log('looks like update installed')
  setTimeout(() => {
    const newConfigurationData = window.krustGetConfigs()
    if (!newConfigurationData || !applyNewConfiguration) {
      return
    }
    newConfigurationData.contentPath = window.getConfigFolder()
    applyNewConfiguration(newConfigurationData)
  }, 0)
})

ipcRenderer.on('settings-reset', () => {
  console.log('looks like settings were reset')

  console.log(currentConfig)

  settingsReset = true
  applyNewConfiguration(currentConfig)
})

function App() {
  const [rootConfig, setRootConfig] = useState(null)
  const [configurationData, setConfigurationData] = useState(null)
  const [modifiers, setModifiers] = useState(defaultModifiers)
  const [licenseStatus, setLicenseStatus] = useState(licenseStatuses.checking)
  const [licenseCodeStrId, setLicenseCodeStrId] = useState(null)
  const [terminalLocation, setTerminalLocation] = useState(null)
  const [temperatureOutside, setTemperatureOutside] = useState()
  const [temperatureInside] = useTemperatureSensorData()
  const [ws, setWs] = useState(null)

  const previousKioskModeEnabledValue = useRef(null)

  applyNewConfiguration = setConfigurationData
  currentConfig = configurationData

  // Данные из "оставьте отзыв"
  const [review, setReview] = useState(defaultReview)

  const [isVoiceEnabled, setVoice] = useState(false)

  // console.log('rootConfig', rootConfig)

  const hasValidLicense = licenseStatus === licenseStatuses.valid
  const isCheckingLicense = licenseStatus === licenseStatuses.checking
  const hasInvalidLicense = licenseStatus === licenseStatuses.invalid ||
    licenseStatus === licenseStatuses.rejected ||
    licenseStatus === licenseStatuses.unknown

  const voiceInductionToggle = () => {
    setVoice(!isVoiceEnabled)
  }

  function pingAndHandleLicense() {
    const hasValidSavedLicense = ipcRenderer.sendSync('ensure-license-key-is-valid')

    console.log('hasValidSavedLicense', hasValidSavedLicense)

    if (!hasValidSavedLicense) { //removed license.key file or first activation
      setLicenseStatus(licenseStatuses.unknown)
    } else {
      (async () => {
        const response = await checkLicense()

        if (response.errorMessage && response.errorMessage.includes('the network is offline')) {
          console.log('network is offline, so we think that code is valid')
          setLicenseStatus(licenseStatuses.valid)
          return;
        }

        if (response.isValid) {
          setLicenseStatus(licenseStatuses.valid)
          setLicenseCodeStrId(response.licenseCodeStrId)

          const softwareVersion = ipcRenderer.sendSync('get-software-version')

          pingServer(softwareVersion, response.licenseCodeStrId).then((data) => {
            if (data && data.location) {
              setTerminalLocation({ lat: data.location.geo_lat, lon: data.location.geo_lon })
            }
          }).catch((e) => console.log('an error occured while attempting to ping server', e))
        }
        else if (response.isValid === false) {
          setLicenseStatus(licenseStatuses.rejected)
          setLicenseCodeStrId(response.licenseCodeStrId)
        }
        else {
          setLicenseStatus(licenseStatuses.invalid)
        }
      })()
    }
  }

  useEffect(() => {
    pingAndHandleLicense()
  }, [])

  useEffect(() => {
    ipcRenderer.on('license-check-request', pingAndHandleLicense)

    return () => {
      ipcRenderer.off('license-check-request', pingAndHandleLicense)
    }
  }, [licenseCodeStrId, licenseStatus])

  useEffect(() => {
    if (rootConfig && terminalLocation) {
      getWeather(terminalLocation).then((data) => {
        if (data && data.fact && data.fact.temp) {
          setTemperatureOutside(data.fact.temp)
        }
      })
    }
  }, [terminalLocation, rootConfig])

  useEffect(() => {
    console.log('sr', settingsReset)
  }, [settingsReset])

  if (settingsReset) {
    settingsReset = false

    console.log(settingsReset)

    setRootConfig(null)
    setConfigurationData(null)
  }

  useEffect(() => {
    // В on mount загружаем конфиг из рутового потока
    const newRootConfig = window.krustGetRootConfig()
    const newConfigurationData = window.krustGetConfigs()

    if (!newRootConfig) {
      // иначе ниже будет попытка достать serverHost, которого нет
      return
    }

    setRootConfig(newRootConfig)
    setHost(newRootConfig.serverHost)
    setTerminalName(newRootConfig.name)

    if (newConfigurationData) {
      newConfigurationData.contentPath = window.getConfigFolder()
      setConfigurationData(newConfigurationData)
    }
  }, [])

  useEffect(() => {
    // На каждый новый рут конфиг создаём новый ws
    console.log('rootConfig', rootConfig)
    if (!rootConfig) {
      // конфиг еще не пришёл
      console.log('!!!no root config')
      return
    }

    const socket = io(rootConfig.serverHost)
    console.log('connecting to ', rootConfig.serverHost)

    initSignaling(socket)

    socket.on('connect', () => {
      console.log('ws connected')

      socket.emit('identify', {
        name: rootConfig.name
      })
    })

    socket.on('update-is-ready', (data) => {
      console.log('Socket: update is ready', data)
      if (data && data.configuration) {
        window.krustUpdateIsReady(data)
      }
    })

    socket.on('ask-clean-cache', () => {
      ipcRenderer.sendSync('ask-clean-cache')
    })

    socket.on('ask-reset-settings', () => {
      ipcRenderer.sendSync('ask-reset-settings')
    })

    socket.on('textMessage', (data) => {
      console.log('new text message!', data)
    })
    //
    setWs(socket)

    return () => {
      socket.close()
    }
  }, [rootConfig])

  useEffect(() => {
    if (!configurationData || !rootConfig) {
      return
    }

    console.log('config data', configurationData)

    setModifiers(defaultModifiers)

    const updateFunc = () => {
      fetchUpdateInfo(rootConfig.name, configurationData.version)
        .then((result) => {
          console.log('update info', result)
          if (result && result.configuration) {
            window.krustUpdateIsReady(result)
          }
        })
        .catch((err) => console.log('get update error', err))
    }

    updateFunc()

    const ti = setInterval(updateFunc, 1000 * 60 * 15) // раз в 15 минут чекаем сами
    return () => {
      clearInterval(ti)
    }
  }, [configurationData, rootConfig])

  useEffect(() => {
    if (!configurationData || !rootConfig) {
      return
    }

    const { kioskModeEnabled } = configurationData

    if (previousKioskModeEnabledValue.current || previousKioskModeEnabledValue.current === false) { //not an initial launch 
      ipcRenderer.sendSync('toggle-kiosk-mode', Boolean(kioskModeEnabled))
    }

    previousKioskModeEnabledValue.current = configurationData.kioskModeEnabled
  }, [configurationData, rootConfig])

  const [isMagnifier, setMagnifier] = useMagnification()

  const [isTooHigh, setTooHigh] = useState(false)

  const onLicenseStatusChange = (licenseStatus) => {
    console.log('license status changed to', licenseStatus)

    setLicenseStatus(licenseStatus)
  }

  const onLicenseCodeStrIdChange = (licenseCode) => {
    console.log('license code changed to', licenseCode)

    setLicenseCodeStrId(licenseCode)
  }

  return (
    <SocketContext.Provider value={ws}>
      <ConfigurationContext.Provider value={configurationData}>
        <TemperatureContext.Provider value={{ temperatureOutside, temperatureInside }}>
          <ModifiersContext.Provider value={{ modifiers, setModifiers }}>
            <ReviewContext.Provider value={{ review, setReview }}>
              <GlobalStyles modifiers={modifiers} />
              <GlobalControls.Provider
                value={{
                  isMagnifier,
                  setMagnifier,
                  isTooHigh,
                  setTooHigh,
                  isVoiceEnabled,
                  voiceInductionToggle
                }}
              >
                {hasInvalidLicense &&
                  <License
                    licenseCodeStrId={licenseCodeStrId}
                    licenseStatus={licenseStatus}
                    onLicenseStatusChange={onLicenseStatusChange}
                    onLicenseCodeStrIdChange={onLicenseCodeStrIdChange}
                  />}
                {isCheckingLicense && <CheckingLicense />}
                {hasValidLicense && rootConfig && configurationData && <Root />}
                {hasValidLicense && !rootConfig && !configurationData && (
                  <StartConfigPage
                    setRootConfig={setRootConfig}
                    setConfigData={setConfigurationData}
                  />
                )}
                {hasValidLicense && rootConfig && !configurationData && <WaitForUpdate />}
              </GlobalControls.Provider>
            </ReviewContext.Provider>
          </ModifiersContext.Provider>
        </TemperatureContext.Provider>
      </ConfigurationContext.Provider>
    </SocketContext.Provider>
  )
}

const mountPoint = document.getElementById('krust-mount-point')
ReactDOM.render(
  <AppContainer>
    <App />
  </AppContainer>,
  mountPoint
)
