import React, { useEffect, useState, useContext, useRef } from 'react'
import nanoid from 'nanoid'
import { Link, Redirect } from 'react-router-dom'
import fs from 'fs'
import Promise from 'bluebird'

import ScreenContentWrapper from './ScreenContentWrapper'
import { VoiceIcon, SwitchCamIcon } from './svg/chatIcons'
import { ReviewContext, HighButtonContext } from '../contexts'

import './VideoRecord.css'

import { useCams } from './cams'
import { useKeyboardScreenStyle } from './effects-ui'

import { getVideoStream } from '../utils/manipulateVideoStreams'

// let currentMediaDeviceId,
let media
let recordChunk = null
let currentCam = null
let recorders = []
// let recorder = null

const VIDEO_TIMER_LIMIT = 120 // seconds

function stopMediaStream(mediaStream) {
  mediaStream.getTracks().forEach((track) => {
    track.stop()
  })
}

function readBuffer({ chunk, filePattern, i }) {
  return new Promise((resolve, reject) => {
    const fileName = `${filePattern}-${i}_.webm`
    const fr = new FileReader()
    const blob = new Blob(chunk)

    console.log(i, 'blob', blob)

    resolve(blob)

    fr.readAsArrayBuffer(blob)
    fr.onload = () => {
      let buffer = new Buffer.from(fr.result)

      fs.writeFile(fileName, buffer, function (err) {
        if (err) {
          console.error('An error ocurred creating the file ' + err.message)
        }
      })
    }
  })
}

function stopRecroder(recorder) {
  return new Promise((resolve) => {
    recorder.onstop = (e) => {
      resolve()
    }
    recorder.stop()
  })
}

async function stopRecord({ recorders, recordChunk, setReview }) {
  const filePattern = `tmp/${Date.now()}-${nanoid(4)}`

  const tmpPathExists = fs.existsSync('tmp')

  if (!tmpPathExists) fs.mkdirSync('tmp')

  const taskList = []

  await Promise.map(recorders, stopRecroder)

  for (let i = 0; i < recordChunk.length; i++) {
    const chunk = recordChunk[i]
    console.log('chunk', i, chunk)

    taskList.push({
      i,
      chunk,
      filePattern
    })
  }

  Promise.map(taskList, readBuffer, { concurrency: 1 }).then((blobList) => {
    console.log('media chunks count:', blobList.length)

    setReview((review) => {
      return {
        ...review,
        chunksCount: blobList.length,
        kind: 'video',
        blobList
      }
    })
  })
}

export default function VideoRecord() {
  const [time, setTime] = useState(VIDEO_TIMER_LIMIT)
  const { setReview } = useContext(ReviewContext)
  const [isTimedOut, setTimedOut] = useState(false)

  const videoRef = useRef(null)
  const cams = useCams()
  const isHighButton = useContext(HighButtonContext)
  const [isRightCam, setRightCam] = useState(true)

  useEffect(() => {
    const mainContainer = document.querySelector('.mainContentContainer')
    if (isHighButton) {
      mainContainer.style.top = '20%'
    } else {
      mainContainer.style.top = ''
    }
    return () => {
      mainContainer.style.top = ''
    }
  }, [isHighButton])

  useKeyboardScreenStyle()

  const changeCamera = () => {
    setRightCam(!isRightCam)
  }

  useEffect(() => {
    setReview((r) => {
      return {
        ...r,
        kind: 'video'
      }
    })
  }, [])

  useEffect(() => {
    if (!cams) {
      return
    }

    currentCam = isHighButton ? 'right' : 'top'
    const deviceId = cams[currentCam]

    // let recorder
    recorders = []

    // let media
    getVideoStream(deviceId).then(
      (localMediaStream) => {
        media = localMediaStream

        if (!videoRef.current) {
          return
        }

        const video = videoRef.current
        video.srcObject = media

        const recorder = new MediaRecorder(media, {
          mimeType: 'video/webm; codecs=vp8'
        })
        recorders.push(recorder)
        recordChunk = [[]]
        recorder.ondataavailable = (e) => {
          recordChunk[0].push(e.data)
        }
        recorder.start()

        video.onloadedmetadata = (e) => {
          video.play()
        }
      },
      (err) => {
        console.error('Failed to get local stream', err)
      }
    )

    return () => {
      if (media) {
        stopMediaStream(media)
      }

      stopRecord({ recorders, setReview, recordChunk })
    }
  }, [cams])

  useEffect(() => {
    console.log('Try to change camera')

    if (!media || !cams) {
      return
    }

    let newCam
    if (!isHighButton) {
      newCam = 'top'
    } else {
      newCam = isRightCam ? 'right' : 'left'
    }

    if (newCam !== currentCam) {
      // change cam
      if (media) {
        stopMediaStream(media)
        media = null
      }

      const deviceId = cams[newCam]
      getVideoStream(deviceId)
        .then(async (localVideoStream) => {
          media = localVideoStream

          const video = videoRef.current
          video.srcObject = media

          const recorder = new MediaRecorder(media, {
            mimeType: 'video/webm; codecs=vp8'
          })
          recorders.push(recorder)
          recordChunk.push([])
          const myIndex = recordChunk.length - 1

          recorder.ondataavailable = (e) => {
            recordChunk[myIndex].push(e.data)
          }
          recorder.start()

          currentCam = newCam
        })
        .catch((e) => {
          console.log('change camera error', e)
        })
    }

    return () => {
      // if (recorder) {
      //   recorder.stop()
      // }
    }
  }, [cams, isRightCam, isHighButton, videoRef])

  useEffect(() => {
    let intervalId = setInterval(() => {
      setTime((time) => {
        if (time > 0) {
          return time - 1
        } else {
          setTimedOut(true)
        }
      })
    }, 1000)

    return () => {
      clearInterval(intervalId)
    }
  }, [])

  if (isTimedOut) {
    return <Redirect to={{ pathname: '/review/stars' }} />
  }

  const videoStyles = !isHighButton ? {} : { transform: 'rotate(-90deg)' }

  return (
    <div>
      <ScreenContentWrapper>
        <div className='title title__video-feedback'>Ваш видеоотзыв</div>
        <div className='videoBlock'>
          <video className='video' muted ref={videoRef} style={videoStyles} />
        </div>
        <div className='b-video-record_switch-cam' onClick={changeCamera}>
          <SwitchCamIcon />
        </div>
        <div className='speakTitle'>Говорите. Идёт запись.</div>
        <div className='videoVoiceIcon'>
          <VoiceIcon />
        </div>
        <Link className='reviewButtonBig' to='/review/stars'>
          Указать оценку
        </Link>
      </ScreenContentWrapper>
    </div>
  )
}
