import React, { useContext, useEffect, useState } from 'react'
import { ExternalWebviewKeyboardContext } from '../contexts'
import Keyboard from './Keyboard'
import { extWvEmitter, extWvEventPrefix } from './Root'
import { applyForAll } from '../iframeHelpers'
import path from 'path'

export default function ExternalWebview({ section, isFullScreen }) {
  const messagePrefix = 'very-unique-krust-kiosk-prefix'
  const EVENT_NAMES = { FOCUS: 'focus', BLUR: 'blur' }
  const [textInput, setTextInput] = useState('')
  const [websitePdfShown, setWebsitePdfShown] = useState(false)

  const { showExternalWebviewKeyboard, setShowExternalWebviewKeyboard } =
    useContext(ExternalWebviewKeyboardContext)

  const isKeyboardClick = (path) => {
    let result = false

    path.forEach(({ className }) => {
      if (['keyboardWrapper', 'keyboard'].includes(className)) result = true
    })

    return result
  }

  const onDomReady = () => {
    applyForAll()
        
    document.onclick = (e) => {
      const keyboardClick = isKeyboardClick(e.path)

      if (!keyboardClick) setShowExternalWebviewKeyboard(false)
    }
  }

  const changeText = (text) => {
    setTextInput(text)
  }

  const clearText = () => {
    setTextInput(textInput.slice(0, -1))
  }

  useEffect(() => {
    if (textInput) {
      const webview = document.querySelector('#wv')

      if (webview && !webview.isLoading()) {
        webview.send('webview-focused-input-change', {
          textInput
        })
      }
    }
  }, [textInput])

  const onEnterPress = () => {
    const webview = document.querySelector('#wv')

    const keyCode = '\u000d'

    webview.sendInputEvent({ type: 'char', keyCode: keyCode, charCode: 13 })
  }

  const onMessageFromWebview = (e) => {
    const webview = document.querySelector('#wv')

    const { message, elementValue } = e.args[0]

    if (true) {
      const rawEvent = message.replace(messagePrefix, '')
      const webviewHasFocus = document.activeElement === webview

      const event = rawEvent.includes(EVENT_NAMES.FOCUS)
        ? EVENT_NAMES.FOCUS
        : rawEvent.includes(EVENT_NAMES.BLUR)
        ? EVENT_NAMES.BLUR
        : null

      if (!event) return

      if (event === EVENT_NAMES.FOCUS) {
        setShowExternalWebviewKeyboard(false)

        setTextInput(elementValue)

        extWvEmitter.emit(extWvEventPrefix, {
          type: 'webviewInputChangeRequest',
          text: elementValue
        })
      } else if (event === EVENT_NAMES.BLUR) {
        if (webviewHasFocus) setShowExternalWebviewKeyboard(false)
      }
    }
  }

  const handleExtWvKeyboardEvent = (args) => {
    const { type } = args

    switch (type) {
      case 'enterPress':
        onEnterPress()
        break
      default:
        return
    }
  }

  const onGoBackClick = () => {    
    const webview = document.querySelector('#wv')
    webview.src = section.uri

    setWebsitePdfShown(false)
  }

  useEffect(() => {
    const webview = document.querySelector('#wv')
    webview.addEventListener('dom-ready', onDomReady)
    webview.addEventListener('ipc-message', onMessageFromWebview)
    webview.addEventListener('console-message', (e) => {
      console.log('webview message', e.message)
    })
    
    webview.addEventListener('new-window', (e) => {
      console.log('new window opened')
      console.log(e.url)

      setWebsitePdfShown(false)

      if (e.url.endsWith('.pdf')) {
        webview.src = `https://docs.google.com/gview?embedded=true&url=${e.url}`        
      }            

      if (webview.src.endsWith('.pdf')) {
        setWebsitePdfShown(true)
      }
    })

    extWvEmitter.on(extWvEventPrefix, handleExtWvKeyboardEvent)

    return () => {
      document.onclick = null

      webview.removeEventListener('dom-ready', onDomReady)
      webview.removeEventListener('ipc-message', onMessageFromWebview)

      extWvEmitter.off(extWvEventPrefix, handleExtWvKeyboardEvent)

      setShowExternalWebviewKeyboard(false)
    }
  }, [])

  const webview = document.querySelector('#vw')

    if (webview && webview.src) {
      if (webview.src.endsWith('.pdf')) {        
        setWebsitePdfShown(true)
      }
      else {
        setWebsitePdfShown(false)
      }
    }    
    
  const isLandscape = window.innerWidth > window.innerHeight
  const is4K = window.innerWidth > 3500

  const webviewWrapperStyle = {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    flexDirection: 'column',
    width: '100%',
    height: isLandscape ? '90%' : '100%',
    padding: is4K ? 0 : 10
  }

  const goBackButtonStyle = {
    width: 'vw',
    height: 60,
    backgroundColor: '#fff',
    position: 'absolute',
    zIndex: 10,
    cursor: 'pointer',
    top: 20,
    paddingRight: 10,
    paddingLeft: 10,
    borderRadius: 10,
    boxShadow: '0px 0px 11px #e3e3e3'
  }

  const minimizedWebviewStyle = {
    display: 'flex',
    width: '608px',
    flexDirection: 'column'
  }

  const maximizedWebviewStyle = {
    ...minimizedWebviewStyle,
    width: is4K ? '66vw' : '100vw',
    height: '100%'
  }

  const keyboardStyle = {
    width: '100%'
  }

  const preloadScriptPath = path.resolve('./src/utils/webviewPreloadScript.js')

  const useragent =
    'Mozilla/5.0 (Linux; Android 9; SM-G960F Build/PPR1.180610.011; wv) AppleWebKit/537.36 (KHTML, like Gecko) Version/4.0 Chrome/74.0.3729.157 Mobile Safari/537.36'
  return (
    <div style={webviewWrapperStyle}>
      {
        websitePdfShown &&
        <div 
          onClick={onGoBackClick}
          style={goBackButtonStyle}
        >
          <h4>Вернуться назад</h4>
        </div>
      }

      <webview
        id={'wv'}
        useragent={useragent}
        preload={preloadScriptPath}
        src={section.uri}
        className='contentFrame contentFrame__ext'
        style={maximizedWebviewStyle}
      />
      {showExternalWebviewKeyboard && (
        <Keyboard
          textInput={textInput}
          changeText={changeText}
          clearText={clearText}
          onEnter={onEnterPress}
          style={keyboardStyle}
        />
      )}
    </div>
  )
}
