import React, { useCallback, useContext, useEffect, useRef, useState } from 'react'
import { Redirect, Link } from 'react-router-dom'
import classNames from 'classnames'
import { desktopCapturer } from 'electron'
import { getVideoStream, setVideoStream } from '../utils/manipulateVideoStreams'
import { SocketContext, HighButtonContext } from '../contexts'
import { VoiceIcon, SwitchCamIcon } from './svg/chatIcons'
import { DoctorIcon } from './svg/userIcons'
import OperatorChatFinish from './OperatorChatFinish'
import saveAndSendVideo from '../utils/saveAndSendVideo'

import { useCams } from './cams'
import './VideoCall.css'

import {
  createPeerConntection,
  addStreamToConnection,
  createOffer
} from '../webrtc'

const DEFAULT_TIMER = 10 // in sec, yes

let currentVideoStream = null
let currentCam = null
let currentPC = null

export default function VideoCall({ dontShowTopControls, sosCall }) {
  const [operator, setOperator] = useState(null)

  const [isCallFinished, setCallFinishedFlag] = useState(false)
  const [callFinishReason, setCallFinishReason] = useState(null)
  const [additionalCallFinishInfo, setAdditionalCallFinishInfo] = useState('')
  const [isAudioOnly, setAudioOnly] = useState(false)
  const [isRightCam, setRightCam] = useState(true)
  const [webcamAvailable, setWebcamAvailable] = useState(true)

  const videoWebcamRef = useRef(null)
  const videoIncomeRef = useRef(null)
  const videoConnectionRef = useRef(null)

  const [isFinishShown, setFinishShowFlag] = useState(false)
  const [isRedirectToMain, setRedirectToMainFlag] = useState(false)
  const [timerCount, setTimerCount] = useState(DEFAULT_TIMER)

  const socket = useContext(SocketContext)
  const [videoConnection, setVideoConnection] = useState(null)
  const [previousVideoConnection, setPreviousVideoConnection] = useState(null)
  const mediaRecorderRef = useRef(null)

  const isHighButton = useContext(HighButtonContext)

  const noOperatorText = sosCall ? (
    <h2 className='operatorChatConnectionTitle secondaryColor'>
      Не беспокойтесь.
      <br />
      Сотрудник уже в пути.
    </h2>
  ) : (
    <h2 className='operatorChatConnectionTitle secondaryColor'>
      Соединяем со свободным
      <br />
      оператором
    </h2>
  )
  
  const cams = useCams()

  useEffect(() => {
    if (videoConnection) {
      videoConnectionRef.current = videoConnection
    }
  }, [videoConnection])

  useEffect(() => {    
    const mediaDevices = navigator.mediaDevices;

    if (!mediaDevices || !mediaDevices.enumerateDevices) 
      return;
  
    mediaDevices.enumerateDevices().then((devices) => {
      setWebcamAvailable(Boolean(devices.some(device => 'videoinput' === device.kind)))           
    })    
  }, [])

  useEffect(() => {
    if (!socket) {
      return;
    }

    console.log(`webcam is${!webcamAvailable ? ' not' : ''} available`)

    if (webcamAvailable) {
      return
    }
    
    console.log('sending emergency call event')
    
    const rootConfig = window.krustGetRootConfig()

    console.log(rootConfig)

    socket.emit('emergency:start-call', { terminalName: rootConfig.name })
  }, [webcamAvailable, socket])

  useEffect(() => {
    return () => {
      const rootConfig = window.krustGetRootConfig()

      socket.emit('emergency:end-call', { terminalName: rootConfig.name })

      if (!videoConnectionRef.current) {
        return;
      }

      const { connectionId } = videoConnectionRef.current      
      
      socket.emit('video-call:close', { connectionId })      
    }
  }, [])
  
  useEffect(() => {
    if (!socket || !cams) {
      return
    }

    console.log('init call')
    let pc = null
    let connectionId = null

    //|| !videoWebcamRef.current || !videoIncomeRef.current ||

    // const video = videoWebcamRef.current
    currentCam = isHighButton ? 'right' : 'top'
    const deviceId = cams[currentCam]

    getVideoStream(deviceId).then(async (localVideoStream) => {
      currentVideoStream = localVideoStream
      // setVideoStream(video, localVideoStream)

      const createdConnection = createPeerConntection(
        socket,
        null,
        (remoteStream) => {
          if (videoIncomeRef.current) {
            videoIncomeRef.current.srcObject = remoteStream
          }
        }
      )

      pc = createdConnection.pc
      connectionId = createdConnection.connectionId

      currentPC = pc

      addStreamToConnection(pc, localVideoStream)

      setVideoConnection({ pc, connectionId })
      const offer = await createOffer(pc)

      socket.emit('video-call:init', {
        connectionId,
        offer: offer.sdp
      })
    })

    socket.on('videoAnswer', (data) => {
      console.log('videoAnswer', data)
      setOperator(data)
    })

    socket.on('video-call:no-operators-to-answer', () => {
      const mediaDevices = navigator.mediaDevices;
      mediaDevices.enumerateDevices().then((devices) => {
      console.log('video-call:no-operators-to-answer')
        const hasCamera = Boolean(devices.some(device => 'videoinput' === device.kind))

        if (hasCamera) {
          setCallFinishedFlag(true)
          setCallFinishReason('Нет доступных операторов')
        }
      })          
    })

    const onVideoClose = (data) => {
      if (data.connectionId === connectionId) {
        //mediaRecorderRef.current.stop();

        console.log('Current connection is closing')
        // onDiscardCall()
        if (pc) {
          pc.close()
        }
        // setFinishShowFlag(true)
        setCallFinishedFlag(true)
      }
    }

    // const onNoOperatorsToAnswer = (data) => {
    //   setAdditionalCallFinishInfo(
    //     'Нет доступных операторов. Невозможно совершить вызов.'
    //   )
    //   onVideoClose(data)
    // }

    socket.on('video-call:close', onVideoClose)

    return () => {
      if (currentVideoStream) {
        onCancelClick()
        currentVideoStream.getTracks().forEach((track) => {
          track.stop()
        })
        currentVideoStream = null
      }
    }
  }, [socket, cams, videoIncomeRef])

  useEffect(() => {
    console.log(
      'onOperator webcam',
      videoWebcamRef.current,
      'stream',
      currentVideoStream
    )
    if (videoWebcamRef.current && currentVideoStream) {
      setVideoStream(videoWebcamRef.current, currentVideoStream)
    }
  }, [videoWebcamRef, operator])

  useEffect(() => {    
    desktopCapturer.getSources({types: ['window', 'screen']}).then(async (inputSources) => {
      const terminalWindowId = inputSources.filter(source => source.name === "Круст.Терминал")[0].id;
      const screenConstraints = {
        audio: {
          mandatory: {
            chromeMediaSource: 'desktop',
            chromeMediaSourceId: terminalWindowId
          }
        },
        video: {
          mandatory: {
            chromeMediaSource: 'desktop',
            chromeMediaSourceId: terminalWindowId
          }
        }
      };
    
            
      if (videoWebcamRef && operator) {      
        const recordedChunks = [];
    
        const handleDataAvailable = (e) => {
          console.log('data', e.data)

          recordedChunks.push(e.data);
        }

        async function handleStop() {
          console.log('rec chunks', recordedChunks, 'operator', operator)

          const endTime = Date.now()

          const rootConfig = window.krustGetRootConfig()
      
          saveAndSendVideo(recordedChunks, operator, endTime - startTime, rootConfig.name)
        }
  
        const screenStream = await navigator.mediaDevices.getUserMedia({ audio: true, video: true })
        const mediaRecorder = new MediaRecorder(screenStream, { mimeType: 'video/webm; codecs=vp9' });
        
        mediaRecorder.start(3000);
        console.log('media recorder started')
        const startTime = Date.now();
        
        mediaRecorder.ondataavailable = handleDataAvailable;
        mediaRecorder.onstop = handleStop   


        mediaRecorderRef.current = mediaRecorder
      }
      else {
        if (mediaRecorderRef.current) {
          //mediaRecorderRef.current.stop();
        }        
      }   
    })
  }, [videoWebcamRef, operator])

  useEffect(() => {
    return () => {
      if (mediaRecorderRef.current) {
        mediaRecorderRef.current.stop();
      }
    }
  }, [])

  useEffect(() => {
    console.log('Try to change camera')

    if (!currentVideoStream || !currentPC) {
      return
    }

    let newCam
    if (!isHighButton) {
      newCam = 'top'
    } else {
      newCam = isRightCam ? 'right' : 'left'
    }
    console.log('cam state', { newCam, currentCam })

    if (newCam !== currentCam) {
      // change cam
      if (currentVideoStream) {
        currentVideoStream.getTracks().forEach((track) => {
          track.stop()
        })
        currentVideoStream = null
      }

      const deviceId = cams[newCam]
      getVideoStream(deviceId)
        .then(async (localVideoStream) => {
          if (videoWebcamRef.current) {
            setVideoStream(videoWebcamRef.current, localVideoStream)            
          }

          currentVideoStream = localVideoStream
          localVideoStream.getAudioTracks().forEach((track) => {
            const sender = currentPC.getSenders().find((s) => {
              return s.track.kind == track.kind
            })
            sender.replaceTrack(track)
          })

          localVideoStream.getVideoTracks().forEach((track) => {
            const sender = currentPC.getSenders().find((s) => {
              return s.track.kind == track.kind
            })
            sender.replaceTrack(track)
          })

          currentCam = newCam
        })
        .catch((e) => {
          console.log('change camera error', e)
        })
    }
  }, [cams, isRightCam, isHighButton, videoWebcamRef])

  useEffect(() => {
    if (!isFinishShown) {
      setTimerCount(DEFAULT_TIMER)
      return
    }
    const ti = setInterval(() => {
      setTimerCount((timerCount) => timerCount - 1)
    }, 1000)
    const to = setTimeout(() => {
      setRedirectToMainFlag(true)
    }, DEFAULT_TIMER * 1000)
    return () => {
      clearInterval(ti)
      clearTimeout(to)
    }
  }, [isFinishShown])

  useEffect(() => {
    if (isFinishShown || isCallFinished) {
      document.body.classList.remove('global_keyboard_shown')
      return
    }

    if (operator) {
      document.body.classList.add('global_keyboard_shown')
    } else {
      document.body.classList.remove('global_keyboard_shown')
    }
    return () => {
      document.body.classList.remove('global_keyboard_shown')
    }
  }, [operator, isFinishShown, isCallFinished])

  const onCancelClick = () => {
    if (mediaRecorderRef.current) {
      //mediaRecorderRef.current.stop();
    }

    setFinishShowFlag(false)
  }

  const onConfirmClick = () => {
    if (socket && videoConnection) {
      //const { connectionId } = videoConnection
      //socket.emit('video-call:close', { connectionId })
    }
    setRedirectToMainFlag(true)
  }

  const changeCamera = () => {
    setRightCam(!isRightCam)
  }

  const cl = classNames({
    videoWrapper: true,
    videoWrapper__hidden: isFinishShown
  })

  if (isRedirectToMain) {
    return <Redirect to={{ pathname: '/' }} />
  }

  if (isCallFinished) {
    return (
      <>
        <div className='video_connections'>
          <h2 className='operatorChatConnectionTitle secondaryColor'>
            Вызов завершён
            {
              callFinishReason &&
              <>
                <br/>
                {callFinishReason}
              </>
            }
          </h2>
          {additionalCallFinishInfo && (
            <p className='operatorChatConnectionDescription'>
              {additionalCallFinishInfo}
            </p>
          )}
        </div>
      </>
    )
  }

  if (!operator) {
    return (
      <>
        <div className='video_connections'>
          <div className='operatorChatConnectionLoading'>
            <div className='Spinner' />
          </div>
          {noOperatorText}
          <Link
            className='operatorGoToTextChat secondaryColor'
            to='/chat'
            data-voicecontrol='Текстовый чат'
          >
            Текстовый чат
          </Link>
        </div>
      </>
    )
  }

  const videoStyles = !isHighButton ? {} : { transform: 'rotate(-90deg)' }

  return (
    <>
      {isFinishShown && (
        <OperatorChatFinish
          timerCount={timerCount}
          onCancelClick={onCancelClick}
          onConfirmClick={onConfirmClick}
          videoCall
        />
      )}
      <div className={cl}>
        {sosCall && <div className='video_sosCall' />}
        {operator && <div className='video_title'>Оператор {operator}</div>}
        <div className='video_myCam'>
          <video
            className='webcamVideo'
            ref={videoWebcamRef}
            id='webcam'
            autoPlay
            muted
            style={videoStyles}
          />
        </div>
        <div className='icon_changeCam' onClick={changeCamera}>
          <SwitchCamIcon />
        </div>
        <div className='video_incoming'>
          <video
            className='incomingVideo'
            ref={videoIncomeRef}
            autoPlay
            id='incomingVideo'
          />
          {isAudioOnly && (
            <div className='doctorIcon'>
              <DoctorIcon />
            </div>
          )}
        </div>
        <div className='video_speekNow'>Говорите</div>
        <div className='video_voiceIcon'>
          <VoiceIcon />
        </div>
        <Link className='reviewButtonBig' to='/' onClick={onCancelClick}>
          Отменить
        </Link>
      </div>
    </>
  )
}
