import { useEffect, useState } from 'react'

export function useCams() {
  const [cams, setCams] = useState(null)

  useEffect(() => {
    const config = window.krustGetRootConfig();
    if (config) {
      console.log('cams set', config.cams)
      setCams(config.cams)
    }
  }, [])

  return cams
}