import React, { useState, useContext, useEffect } from 'react'
import { Link } from 'react-router-dom'

import { ReviewContext, HighButtonContext } from '../contexts'
import Keyboard from './Keyboard'
import './Review.css'

export default function ReviewPhone() {
  const [phone, setPhone] = useState('+7 ')
  const { setReview, review } = useContext(ReviewContext)

  const isTooHight = useContext(HighButtonContext)

  useEffect(() => {
    const mainContainer = document.querySelector('.mainContentContainer')
    if (isTooHight) {
      mainContainer.style.top = '25%'
    } else {
      mainContainer.style.top = ''
    }
    return () => {
      mainContainer.style.top = ''
    }
  }, [isTooHight])

  // Записал данные в глобальный state
  const changeData = () => {
    // Номер телефона без знаков и скобок
    const valuePhone =
      phone === '+7 ' ? 'Телефон не заполнялся' : phone.replace(/[^+\d]/g, '')
    setReview({ ...review, phone: valuePhone })
  }

  const clearText = () => {
    switch (phone.length) {
      case 3:
        return
      case 5:
        setPhone(phone.slice(0, -2))
        return
      case 10:
        setPhone(phone.slice(0, -3))
        return
      case 14:
        setPhone(phone.slice(0, -2))
        return
      case 17:
        setPhone(phone.slice(0, -2))
        return
      default:
        setPhone(phone.slice(0, -1))
        return
    }
  }

  const myInput = (text) => {
    if (phone.length === 18) {
      return
    }

    switch (phone.length) {
      case 3:
        setPhone(phone + '(' + text)
        return
      case 7:
        setPhone(phone + ') ' + text)
        return
      case 12:
        setPhone(phone + '-' + text)
        return
      case 15:
        setPhone(phone + '-' + text)
        return
      default:
        setPhone(phone + text)
        return
    }
  }

  return (
    <div className='keyboardScreenContainer'>
      <div className='reviewWrapper'>
        <h5 className='reviewTitle'>4 шаг из 4</h5>
        <h4 className='reviewSubTitle'>Ваш телефон (необязательно)</h4>

        <div className='reviewTextWrap'>
          <div className='reviewText reviewTextPhone'>{phone}</div>
        </div>

        <div className='reviewNumbersKeyboard'>
          <Keyboard
            isKeyboardForPhone={true}
            onKeyPress={myInput}
            clearText={clearText}
          />
        </div>

        <div className='ReviewButtonWrapper'>
          <Link className='ReviewButton reviewButtonBack' to='/review/name'>
            Назад
          </Link>
          <Link
            className='ReviewButton reviewButtonNext'
            to='/review/finish'
            onClick={changeData}
          >
            Отправить
          </Link>
        </div>
        <div className='reviewPhoneComment'>
          Нажимая на кнопку «Отправить» я соглашаюсь на обработку персональных
          данных
        </div>
      </div>
    </div>
  )
}
